<template>
  <div class="CloudMeet_New"
       :style="{ backgroundImage: 'url(' + $root.imgList['bjH5'] + ')' }">
    <div class="main-left">
      <div class="top-list">
        <div class="tit">
          <p>近期会议</p>
          <img src="../../assets/img/meet/down_icon_normal@2x.png"
               alt="" />
        </div>
        <ul>
          <el-card shadow="hover"
                   v-for="item in startMeetingList"
                   :key="item.confId"
                   class="meetCard">
            <div>
              <p class="meetDate">{{ item.startTime.substring(0, 10) }}</p>
              <div class="meetTitle">
                <span>{{
                  item.confName + "（" + item.members.length + "）"
                }}</span>
              </div>
              <div class="meetCardBottom">
                <span>{{
                  item.startTime.substring(11, 16) +
                  " - " +
                  item.endTime.substring(11, 16)
                }}</span>
                <span>{{ item.holder }}</span>
                <span @click="openMeetingInfo(item.confId)">查看</span>
              </div>
            </div>
          </el-card>
          <el-card shadow="hover"
                   v-for="item in reserveMeetingList"
                   :key="item.confId"
                   class="meetCard">
            <div>
              <p class="meetDate">
                {{ item.reserveStartTime.substring(0, 10) }}
              </p>
              <div class="meetTitle">
                <span>{{
                  item.confName + "（" + item.members.length + "）"
                }}</span>
                <span>
                  <el-tag type="danger"
                          size="mini">未开始</el-tag>
                </span>
              </div>
              <div class="meetCardBottom">
                <span>{{
                  item.reserveStartTime.substring(11, 16) +
                  " - " +
                  item.endTime.substring(11, 16)
                }}</span>
                <span>{{ item.holder }}</span>
                <span @click="openMeetingInfo(item.confId)">查看</span>
              </div>
            </div>
          </el-card>
          <p v-if="
              startMeetingList.length == 0 && reserveMeetingList.length == 0
            "
             class="noMeeting">
            无会议！
          </p>
        </ul>
      </div>
      <div class="medium-list">
        <div class="history-tit">历史会议</div>
        <div class="history-list"
             v-if="meetingHistoryList.length > 0">
          <ul v-infinite-scroll="historyLoad"
              infinite-scroll-disabled="historyDisabled">
            <el-card shadow="hover"
                     v-for="(item, index) in meetingHistoryList"
                     :key="index"
                     class="meetCard">
              <div>
                <p class="meetDate">{{ item.startTime.substring(0, 10) }}</p>
                <div class="meetTitle">
                  <span>{{
                    item.confName + "（" + item.members.length + "）"
                  }}</span>
                </div>
                <div class="meetCardBottom">
                  <span>{{
                    item.startTime.substring(11, 16) +
                    " - " +
                    item.endTime.substring(11, 16)
                  }}</span>
                  <span>{{ item.holder }}</span>
                  <span @click="openHistoryMeetingInfo(item)">查看</span>
                </div>
              </div>
            </el-card>
          </ul>
          <p class="historyBottom"
             v-if="loadingHistory">加载中...</p>
          <p class="historyBottom"
             v-if="noMoreHistory">没有更多了</p>
        </div>
        <div class="history-list"
             v-else>
          <p class="noHistoryMeeting">无记录！</p>
        </div>
      </div>
    </div>
    <div class="main-right">
      <div v-if="isShowMainpage"
           style="height: 100%">
        <ul class="right-menu">
          <li>
            <div
              class="con"
              :class="{ active: activeMenu === 0 }"
              @click="clickTab(0)"
            >
              <div class="icon">
                <img
                  src="../../assets/img/meet/join_icon_normal@2x.png"
                  alt=""
                />
              </div>
              <p>{{ $t("meeting.joinMeeting") }}</p>
            </div>
          </li>
          <li>
            <div class="con"
                 :class="{ active: activeMenu === 1 }"
                 @click="clickTab(1)">
              <div class="icon">
                <img src="../../assets/img/meet/startm_icon_normal@2x.png"
                     alt="" />
              </div>
              <p>{{ $t("meeting.haveMeeting") }}</p>
            </div>
          </li>
          <li>
            <div
              class="con"
              :class="{ active: activeMenu === 2 }"
              @click="clickTab(2)"
            >
              <div class="icon">
                <img
                  src="../../assets/img/meet/start_icon_normal@2x.png"
                  alt=""
                />
              </div>
              <p>{{ $t("last.bespeakMeeting") }}</p>
            </div>
          </li>
        </ul>
        <div class="right-main">
          <div class="con join"
               v-if="activeMenu === 0">
            <el-input v-model="confId"
                      type="number"
                      :placeholder="
                $t('meeting.pleaseEnter') + $t('meeting.meetingNumber')
              "
                      prefix-icon="el-icon-user-solid el-icon"></el-input>
            <!--<el-input-->
            <!--v-model="password" type="password"-->
            <!--placeholder="请输入会议密码，无密码时无需提交" prefix-icon="el-icon-s-goods el-icon"></el-input>-->
            <div class="media">
              <span class="flt">{{
                $t("meeting.turnOffVideoWhenJoining")
              }}</span>
              <span class="frt">
                <el-checkbox v-model="isCloseVideo">
                  <img v-if="!isCloseVideo"
                       src="../../assets/img/meet/camera_icon_normal@2x.png"
                       alt="" />
                  <img v-else
                       src="../../assets/img/meet/camera_icon_active@2x@2x.png"
                       alt="" />
                </el-checkbox>
              </span>
            </div>
            <div class="media">
              <span class="flt">{{ $t("meeting.muteWhenJoining") }}</span>
              <span class="frt">
                <el-checkbox v-model="isCloseAudio">
                  <img v-if="!isCloseAudio"
                       src="../../assets/img/meet/mic_icon_normal@2x@2x.png"
                       alt="" />
                  <img v-else
                       src="../../assets/img/meet/mic_icon_active@2x.png"
                       alt="" />
                </el-checkbox>
              </span>
            </div>
            <div class="btns">
              <el-button class="cancel flt">{{ $t("btn.cancel") }}</el-button>
              <el-button type="primary"
                         class="frt"
                         @click="checkConnect(confId)">{{ $t("btn.determine") }}</el-button>
            </div>
          </div>
          <div class="con start"
               v-if="activeMenu === 1 || activeMenu === 2">
            <div class="start-left">
              <ul class="item">
                <li class="confName">
                  <span class="flt"> 会议名称 </span>
                  <div class="frt">
                    <el-input v-model="StarMeetTitle"
                              :placeholder="
                        `${LoginServe.Profile.NickName}` +
                        $t('meeting.sMeeting')
                      "
                              maxlength="20"></el-input>
                    <img src="../../assets/img/meet/start-edit.png"
                         alt="" />
                  </div>
                </li>
                <li class="confStartTime"
                    v-if="activeMenu === 2">
                  <span class="flt"> 会议开始时间 </span>
                  <div class="frt">
                    <el-date-picker ref="dataPicker"
                                    v-model="confStartDate"
                                    type="date"
                                    :clearable="false"
                                    value-format="yyyy-MM-dd"
                                    :picker-options="datePickerOptions"
                                    @change="changeStartDate"
                                    @blur="datePickerBlur($event)"
                                    placeholder="选择日期">
                    </el-date-picker>
                    <el-time-picker ref="timePicker"
                                    value-format="HH:mm"
                                    v-model="confStartTime"
                                    size="small"
                                    format="HH:mm"
                                    :clearable="false"
                                    :picker-options="timePickerOptions"
                                    :disabled="confStartDate ? false : true"
                                    placeholder="选择时间">
                    </el-time-picker>
                  </div>
                </li>
                <li class="confTime">
                  <span class="flt"> 会议时长 </span>
                  <div class="frt">
                    <el-select v-model="SelectTimeHour"
                               :placeholder="$t('meeting.pleaseChoose')"
                               style="width: 152px"
                               @change="selectTimeChangeIs00">
                      <el-option v-for="item in SelectTimeHourList"
                                 :key="item.value"
                                 :label="item.label"
                                 :value="item.value"
                                 :disabled="item.disabled"></el-option>
                    </el-select>

                    <el-select v-model="SelectTimeMin"
                               :placeholder="$t('meeting.pleaseChoose')"
                               style="width: 152px"
                               @change="selectTimeChangeIs001">
                      <el-option v-for="item in SelectTimeMinList"
                                 :key="item.value"
                                 :label="item.label"
                                 :value="item.value"></el-option>
                    </el-select>
                  </div>
                </li>
                <!-- <li class="max-member">
                  <span class="flt"> 会议方数 </span>
                  <div class="frt">
                    <el-input
                      v-model="memberCount"
                      v-if="canSetMemberCount"
                      maxlength="4"
                      type="number"
                      v-on:input="setMemberCount()"
                      :placeholder="
                        enable
                          ? $t('meeting.currentlyTheAvailabel') +
                            `${meetMaxCount}`
                          : $t('meeting.thereNoLimit')
                      "
                    ></el-input>
                  </div>
                </li> -->
                <!-- <li class="password">
                  <div class="tit">
                    <span class="flt"> 会议密码 </span>
                    <el-switch v-model="needPassword"> </el-switch>
                  </div>
                  <div class="pwd">
                    <el-input
                      v-model="meetpwd"
                      maxlength="6"
                      oninput="value=value.replace(/[^\d]/g,'')"
                      placeholder="请输入6位数字密码"
                      :disabled="!needPassword"
                    ></el-input>
                    <img
                      class="flt"
                      src="../../assets/img/meet/start-edit.png"
                      alt=""
                    />
                  </div>
                </li> -->
              </ul>
              <div class="members"
                   v-if="false">
                <div class="tit">
                  <span class="flt">参会人 ({{ membersList.length }})</span>
                  <img src="../../assets/img/meet/start-add.png"
                       alt=""
                       class="flt"
                       @click.prevent="
                      $bus.emit(
                        'addPerson',
                        true,
                        memberCount,
                        membersList,
                        selectMemeberCallBack
                      )
                    " />
                </div>
                <ul>
                  <li v-for="(member, index) in membersList"
                      :key="index">
                    <span v-if="!member.photourl"
                          :style="{ backgroundImage: getcolor(member.account) }">{{ member.username | nameslice }}</span>
                    <img v-else
                         :src="member.photourl"
                         alt="" />
                    <p>{{ member.username }}</p>
                  </li>
                </ul>
              </div>
            </div>
            <div class="start-right">
              <div class="confTopic">
                <div class="tit">
                  会议议题
                  <span class="frt btn"
                        @click="openEditModal">
                    {{ meetingTopicsList.length > 0 ? "编辑" : "添加" }}
                  </span>
                </div>
                <ul v-if="meetingTopicsList.length > 0">
                  <li v-for="(item, index) in meetingTopicsList"
                      :key="index">
                    <span>{{ index + 1 + "." }}</span> {{ item.value }}
                  </li>
                </ul>
                <ul v-else
                    style="height: 15vh">
                  <p style="">您可以添加会议议题!</p>
                </ul>
              </div>
              <div class="cloud-video"
                   v-if="false">
                <div class="tit">云录像</div>
                <ul>
                  <el-radio-group v-model="radio">
                    <el-radio :label="3">主持人视角</el-radio>
                    <el-radio :label="6">参会人视角</el-radio>
                  </el-radio-group>
                  <span style="float: right">选择录像源<i class="el-icon-video-camera-solid"
                       style="margin-left: 10px"></i></span>
                </ul>
              </div>
              <div class="cloud-video"
                   v-if="false">
                <div class="tit">旁路直播</div>
                <ul>
                  <span>直播</span>
                  <span style="float: right"
                        @click="inviteLive">邀请观看直播<i class="el-icon-user-solid"
                       style="margin-left: 10px"></i></span>
                </ul>
              </div>
              <div class="members">
                <div class="tit">
                  <span class="flt">参会人 ({{ membersList.length }})</span>
                  <img src="../../assets/img/meet/start-add.png"
                       alt=""
                       class="flt"
                       @click.prevent="
                      $bus.emit(
                        'addPerson',
                        true,
                        memberCount,
                        membersList,
                        selectMemeberCallBack
                      )
                    " />
                </div>
                <ul>
                  <li v-for="(member, index) in membersList"
                      :key="index">
                    <span v-if="!member.photourl"
                          :style="{ backgroundImage: getcolor(member.account) }">{{ member.username | nameslice }}</span>
                    <img v-else
                         :src="member.photourl"
                         alt="" />
                    <p>{{ member.username }}</p>
                  </li>
                </ul>
              </div>
            </div>
            <el-button type="primary"
                       class="confirm"
                       @click="handleMeetBefore">确定</el-button>
          </div>
        </div>
      </div>
      <meeting-detail ref="meetingDetail"
                      v-show="isShowMeetingInfo"
                      @cancelMeetingInfo="cancelMeetingInfo"
                      :historyConf="historyConf"
                      :confId="meetingInfoId"></meeting-detail>
      <history-meeting-detail ref="historyMeetingDetail"
                              v-show="isShowHistoryMeetingInfo"
                              @cancelMeetingInfo="cancelMeetingInfo"
                              :historyConf="historyConf"
                              :confId="meetingInfoId"></history-meeting-detail>
      <div v-if="false"
           class="meetinfo">
        <div class="meetingTitle">
          <div class="mainTit">
            <span class="title">{{ meetingInfoModel.confName }}</span>
            <span class="meetingStatus"
                  v-if="!isMeetingEnd">
              <el-tag v-if="meetingInfoModel.state"
                      type="danger"
                      effect="dark"
                      size="mini">进行中</el-tag>
              <el-tag v-else
                      type="success"
                      effect="dark"
                      size="mini">未开始</el-tag>
            </span>
            <span class="meetingStatus"
                  v-else>
              <el-tag type="info"
                      effect="dark"
                      size="mini">已结束</el-tag>
            </span>
          </div>
        </div>
        <div class="right-main">
          <div class="con info">
            <el-card shadow="always"
                     class="basicInfoCard">
              <ul>
                <li style="margin-bottom: 10px">
                  <span>会议号：<span>{{ meetingInfoModel.confId }}</span><i class="el-icon-document-copy"
                       style="color: #4f98ff"
                       @click="copyConfId"></i></span>
                  <span style="float: right">创建者：{{ meetingInfoModel.holder }}</span>
                </li>
                <li>
                  <span>时间：{{
                      meetingInfoModel.state
                        ? meetingInfoModel.startTime
                        : meetingInfoModel.reserveStartTime
                    }}</span>
                  <span style="float: right">会议密码：{{
                      meetingInfoModel.password
                        ? meetingInfoModel.password
                        : "无"
                    }}</span>
                </li>
              </ul>
            </el-card>
            <el-card shadow="always"
                     class="topicCard"
                     style="margin-top: 3px">
              <p>
                会议议题<span @click="openEditModal">编辑<img src="../../assets/img/meet/start-edit.png"
                       alt="" /></span>
              </p>
              <ul v-if="meetingTopicsList.length > 0">
                <li v-for="(item, index) in meetingTopicsList"
                    :key="index">
                  <span>{{ index + 1 + "." }}</span> {{ item.value }}
                </li>
              </ul>
              <ul v-else>
                <p style="">您可以添加会议议题!</p>
              </ul>
            </el-card>
            <el-card shadow="always"
                     class="memberCard"
                     style="margin-top: 3px">
              <div class="tit">
                <span class="flt">参会人 ({{ membersList.length }})</span>
                <img src="../../assets/img/meet/start-add.png"
                     alt=""
                     class="flt"
                     @click.prevent="
                    $bus.emit(
                      'addPerson',
                      true,
                      memberCount,
                      membersList,
                      selectMemeberCallBack
                    )
                  " />
                <!-- <img src="../../assets/img/meet/start-del.png" alt="" class="flt"> -->
              </div>
              <ul>
                <li v-for="(member, index) in membersList"
                    :key="index">
                  <span v-if="!member.photourl"
                        :style="{ backgroundImage: getcolor(member.account) }">{{ member.userName || member.username | nameslice }}</span>
                  <img v-else
                       :src="member.photourl"
                       alt="" />
                  <p>{{ member.userName || member.username }}</p>
                </li>
              </ul>
            </el-card>
            <el-card shadow="always"
                     class="videoCard"
                     v-if="!isMeetingEnd">
              <div class="tit">
                <span class="flt">云录像</span>
              </div>
              <ul class="radioUl">
                <el-radio-group v-model="radio">
                  <el-radio :label="3">主持人视角</el-radio>
                  <el-radio :label="6">参会人视角</el-radio>
                </el-radio-group>
                <span style="float: right">选择录像源<i class="el-icon-video-camera-solid"
                     style="margin-left: 10px"></i></span>
              </ul>
            </el-card>
            <el-card shadow="always"
                     class="discussCard">
              <div class="tit">
                <span class="flt">会中讨论</span>
              </div>
              <div class="discussMember">
                <span>全员讨论</span>
              </div>
              <p class="">会议纪要</p>
              <p class="discussContent">
                会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要会议纪要
              </p>
              <p>编辑</p>
            </el-card>
            <el-card shadow="always"
                     class="videoCard"
                     v-if="isMeetingEnd">
              <div class="tit">
                <span class="flt">云录像</span>
              </div>
              <ul class="videoUl">
                <li>
                  <video id="video"
                         controls
                         data-index="1"
                         class="window_img"
                         type="video/mp4"
                         src="http://opm.yuntongxun.com/edm/edm20200610/images/46d0bee8-bb73-11ea-a9e7-7cd30aeb7804.mp4"></video>
                  <span> <i class="el-icon-download"></i>下载</span>
                </li>
                <li>
                  <video id="video"
                         controls
                         data-index="1"
                         class="window_img"
                         type="video/mp4"
                         src="http://opm.yuntongxun.com/edm/edm20200610/images/46d0bee8-bb73-11ea-a9e7-7cd30aeb7804.mp4"></video>
                  <span> <i class="el-icon-download"></i>下载</span>
                </li>
                <li>
                  <video id="video"
                         controls
                         data-index="1"
                         class="window_img"
                         type="video/mp4"
                         src="http://opm.yuntongxun.com/edm/edm20200610/images/46d0bee8-bb73-11ea-a9e7-7cd30aeb7804.mp4"></video>
                  <span> <i class="el-icon-download"></i>下载</span>
                </li>
              </ul>
            </el-card>
            <div v-if="!isMeetingEnd">
              <div class="btns"
                   v-if="meetingInfoModel.state">
                <el-button class="cancel flt"
                           @click="closeMeeting(meetingInfoModel.confId)">结束会议</el-button>
                <el-button type="primary"
                           class="frt"
                           @click="JoinMeet(meetingInfoModel.confId)">重新入会</el-button>
              </div>
              <div class="btns"
                   v-else>
                <el-button class="cancel flt"
                           @click="cancelMeetingInfo">{{
                  $t("btn.cancel")
                }}</el-button>
                <el-button type="primary"
                           class="frt"
                           @click="JoinMeet(meetingInfoModel.confId)">入会</el-button>
              </div>
            </div>
            <div v-else>
              <div class="btns">
                <el-button class="cancel flt"
                           @click="cancelMeetingInfo">{{
                  $t("btn.cancel")
                }}</el-button>
                <el-button type="primary"
                           class="frt">重新开会</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="shadow"
         v-if="isShowShadow">
      <div class="editTopicModal"
           v-if="isShowEditTopic">
        <!-- <span class="frt" @click="submitForm('dynamicValidateForm')">完成</span>		 -->
        <ul style="margin-top: 50px">
          <el-form :model="dynamicValidateForm"
                   ref="dynamicValidateForm"
                   label-width="100px"
                   class="demo-dynamic">
            <el-form-item v-for="(topic, index) in dynamicValidateForm.topics"
                          :label="index + 1 + ''"
                          :key="topic.key"
                          :prop="'topics.' + index + '.value'"
                          :rules="{
                required: true,
                message: '会议议题不能为空',
                trigger: 'blur',
              }">
              <el-input v-model="topic.value"
                        style="width: 80%"
                        maxlength="40"></el-input>
              <el-button icon="el-icon-delete"
                         circle
                         @click.prevent="removeTopic(topic)"></el-button>
              <!-- <el-button @click.prevent="removeTopic(topic)">删除</el-button> -->
            </el-form-item>
          </el-form>
        </ul>
        <span class="frt rx_cancel"
              @click="addTopic">添加</span>
        <div class="topicBottom">
          <span class="rx_cancel cancel"
                @click="cancelTopic">{{
            $t("btn.cancel")
          }}</span>
          <span class="rx_btn confirm"
                @click="submitForm('dynamicValidateForm')">完成</span>
        </div>
      </div>
    </div>
    <el-dialog title="提示"
               :visible.sync="isShowCreate"
               width="30%">
      <p style="text-align: center">{{ dialogContent }}</p>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="isShowCreate = false">取 消</el-button>
        <el-button type="primary"
                   @click="handleMeet">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import meetingDetail from './meetingDetail'
import historyMeetingDetail from './historyMeetingDetail'
export default {
  name: 'cloudMeet',
  data() {
    return {
      activeMenu: 1,
      confId: '',
      password: '',
      needPassword: false,
      isCloseVideo: false,
      isCloseAudio: false,
      StarMeetTitle: '',
      SelectTimeHourList: [
        {
          value: '0',
          label: '0' + this.$t('meeting.hours'),
        },
        {
          value: '60',
          label: '1' + this.$t('meeting.hours'),
        },
        {
          value: '120',
          label: '2' + this.$t('meeting.hours'),
        },
        {
          value: '180',
          label: '3' + this.$t('meeting.hours'),
        },
        {
          value: '240',
          label: '4' + this.$t('meeting.hours'),
        },
        {
          value: '300',
          label: '5' + this.$t('meeting.hours'),
        },
        {
          value: '360',
          label: '6' + this.$t('meeting.hours'),
        },
        {
          value: '420',
