  created() {
    const that = this
    window.addEventListener('message', (event) => {
      const postMessage = event.data
      if (postMessage && postMessage.type === 'meet') {
        let list = []
        const checkedList = postMessage.checkedList
        checkedList && checkedList.map((v) => {
          list = list.concat([
            {
              account: v.phone,
              username: v.name,
            },
          ])
        })
        that.membersList = list
      }
    })
