  methods: {
    getFormatDate: function (now, push) {
      now = now || new Date()
      let nowDate = push ? now.getTime() + 1000 * 60 * 15 : now //预约会议推迟15分钟
      nowDate = new Date(nowDate)
      const year = nowDate.getFullYear()
      const month =
        nowDate.getMonth() + 1 < 10
          ? '0' + (nowDate.getMonth() + 1)
          : nowDate.getMonth() + 1
      const date =
        nowDate.getDate() < 10 ? '0' + nowDate.getDate() : nowDate.getDate()
      const hour =
        nowDate.getHours() < 10 ? '0' + nowDate.getHours() : nowDate.getHours()
      const minute =
        nowDate.getMinutes() < 10
          ? '0' + nowDate.getMinutes()
          : nowDate.getMinutes()
      const seconds =
        nowDate.getSeconds() < 10
          ? '0' + nowDate.getSeconds()
          : nowDate.getSeconds()
      return (
        year +
        '-' +
        month +
        '-' +
        date +
        ' ' +
        hour +
        ':' +
        minute +
        ':' +
        seconds
      )
    },
    clickTab(num) {
      this.membersList = []
      this.activeMenu = num
      this.resetData()
      console.log(this.LoginServe.Profile.NickName)
    },
    resetData() {
      this.StarMeetTitle = ''
      this.SelectTimeHour = ''
      this.SelectTimeMin = ''
      this.confStartTime = ''
      this.confStartDate = ''
      this.memberCount = 20
      this.needPassword = false
      this.meetpwd = ''
      this.membersList = []
      this.meetingTopicsList = []
      this.meetingMembersList = []
      this.isEditReserveMeeting = false
      this.isShowCreate = false
    },
    handleMeet() {
      if (!this.isEditReserveMeeting) {
        this.creatMeet()
      } else {
        this.editMeet()
      }
    },
    handleMeetBefore() {
      this.isShowCreate = true
      this.dialogContent =
        this.activeMenu == 1 ? '是否立即开会？' : '是否预约会议'
      if (this.isEditReserveMeeting) this.dialogContent = '是否保存编辑'
    },
    creatMeet() {
      console.log('LoginServe=============', this.LoginServe)
      let meetTime = 0
      if (this.SelectTimeHour) {
        meetTime = meetTime + parseInt(this.SelectTimeHour)
      }
      if (this.SelectTimeMin) {
        meetTime = meetTime + parseInt(this.SelectTimeMin)
      }
      if (meetTime == 0) {
        meetTime = 120
      }
      console.log(meetTime)
      if (parseInt(this.memberCount) === 0) {
        this.$message({
          message: this.$t('meeting.theNumberMin'),
          type: 'error',
        })
        return
      }
      if (!this.memberCount) {
        this.$message({
          message: this.$t('meeting.pleaseEnterMax'),
          type: 'error',
        })
        return
      }
      if (this.needPassword) {
        if (!this.meetpwd) {
          this.$message({
            message:
              this.$t('meeting.pleaseEnter') +
              this.$t('meeting.meetingPassword'),
            type: 'error',
          })
          return
        }
        if (this.meetpwd.length != 6) {
          this.$message({
            message: '密码必须为六位数',
            type: 'error',
          })
          return
        }
      }
      let topics = []
      this.meetingTopicsList.forEach((item) => {
        topics.push({
          topic: item.value,
        })
      })
      let contentTopics = {
        topics,
      }
      let members = [
        {
          memberId: this.LoginServe.UserId,
          idType: 2,
          userName: this.LoginServe.Profile.NickName,
        },
      ]
      this.membersList.forEach((item) => {
        members.push({
          memberId: item.account,
          idType: 2,
          userName: item.username,
        })
      })
      console.log('members=====================', members)
      let that = this
      console.log('meetTime---', meetTime)
      console.log('Number(this.memberCount)', Number(this.memberCount))
      console.log('meetingTopicsList===========', this.meetingTopicsList)
      let createMeetbuilder = new RL_MEET.CreateMeetBuilder()
      createMeetbuilder.setConfName(
        this.StarMeetTitle
          ? this.StarMeetTitle
          : `${this.LoginServe.Profile.NickName}` + this.$t('meeting.sMeeting')
      )
      let startTime = ''
      if (this.activeMenu == 2) {
        // startTime =  this.confStartTime
        startTime =
          this.confStartDate && this.confStartTime
            ? this.confStartDate + ' ' + this.confStartTime + ':00'
            : this.getFormatDate(new Date(), true)
        console.log(startTime)
        var oDate1 = new Date(startTime)
        var oDate2 = new Date()
        if (oDate1.getTime() < oDate2.getTime()) {
          startTime = this.getFormatDate(new Date(), false)
          console.log(startTime)
        }
        createMeetbuilder.setStartTime(startTime)
      }
      if (this.needPassword) {
        createMeetbuilder.setPassword(this.meetpwd)
      }
      // createMeetbuilder.setConfRoomId(message);
      createMeetbuilder.setDuration(meetTime)
      createMeetbuilder.setRemindBeforeEnd(10)
      createMeetbuilder.setReserveEnable(this.activeMenu == 2 ? 1 : 2) //预约会议
      createMeetbuilder.setSendReserveNote(1) //是否给会议成员发送提醒通知（提醒成员被列入预约会议成员）
      createMeetbuilder.setChatInConf(1)
      createMeetbuilder.setVoiceMode(1)
      // createMeetbuilder.setJoinState(joinstate);
      createMeetbuilder.setMaxMember(Number(this.memberCount))

      createMeetbuilder.setConfTopic(JSON.stringify(contentTopics))
      createMeetbuilder.setConfType(0)
      createMeetbuilder.setMembers(members)
      createMeetbuilder.setAutoRecord(1)
      createMeetbuilder.setRecordAction(40)
      createMeetbuilder.setAutoClose(0)
      console.log('createMeetbuilder=======================', createMeetbuilder)
      RL_MEET.CreateMeet(
        createMeetbuilder,
        (response) => {
          console.log('createMeetbuilder====', response)
          if (response.statusCode == '000000') {
            this.resetData()
            this.updateMeetingList()
            if (this.activeMenu == 2) {
              this.$message('预约会议成功')
            } else {
              this.$router.push({
                path: '/yuntongxun/meeting',
                query: { confId: response.confId },
              })
            }
          } else {
            this.$message({
              message:
                response.data.statusMsg +
                this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          console.log(err)
          if (err.statusCode == '814028') {
            that.conidpop = err.confId
            that.confirmleave = true
          } else {
            that.$message({
              message: err.statusMsg,
              type: 'warning',
            })
          }
        }
      )
    },
    editMeet() {
      let meetTime = 0
      if (this.SelectTimeHour) {
        meetTime = meetTime + parseInt(this.SelectTimeHour)
      }
      if (this.SelectTimeMin) {
        meetTime = meetTime + parseInt(this.SelectTimeMin)
      }
      if (meetTime == 0) {
        meetTime = 120
      }
      console.log(meetTime)
      if (parseInt(this.memberCount) === 0) {
        this.$message({
          message: this.$t('meeting.theNumberMin'),
          type: 'error',
        })
        return
      }
      if (!this.memberCount) {
        this.$message({
          message: this.$t('meeting.pleaseEnterMax'),
          type: 'error',
        })
        return
      }
      if (this.needPassword) {
        if (!this.meetpwd) {
          this.$message({
            message:
              this.$t('meeting.pleaseEnter') +
              this.$t('meeting.meetingPassword'),
            type: 'error',
          })
          return
        }
      }
      let topics = []
      this.meetingTopicsList.forEach((item) => {
        topics.push({
          topic: item.value,
        })
      })
      let contentTopics = {
        topics,
      }
      let members = [
        {
          memberId: this.LoginServe.UserId,
          idType: 2,
          userName: this.LoginServe.Profile.NickName,
        },
      ]
      this.membersList.forEach((item) => {
        members.push({
          memberId: item.account,
          idType: 2,
          userName: item.username,
        })
      })
      console.log(this.StarMeetTitle)
      console.log('members=====================', members)
      let that = this
      console.log('meetTime---', meetTime)
      console.log('Number(this.memberCount)', Number(this.memberCount))
      console.log('meetingTopicsList===========', this.meetingTopicsList)
      let startTime =
        this.confStartDate && this.confStartTime
          ? this.confStartDate + ' ' + this.confStartTime + ':00'
          : this.getFormatDate(new Date(), true)
      console.log(startTime)
      console.log(contentTopics)
      let ConferenceUpdateBuilder = new RL_MEET.ConferenceUpdateBuilder()
      ConferenceUpdateBuilder.setConfId(this.meetingInfoId)
      ConferenceUpdateBuilder.setConfName(
        this.StarMeetTitle
          ? this.StarMeetTitle
          : `${this.LoginServe.Profile.NickName}` + this.$t('meeting.sMeeting')
      )
      if (this.needPassword) {
        ConferenceUpdateBuilder.setPassword(this.meetpwd)
      }
      ConferenceUpdateBuilder.setStartTime(startTime)
      ConferenceUpdateBuilder.setDuration(meetTime)
      ConferenceUpdateBuilder.setMaxMember(Number(this.memberCount))
      ConferenceUpdateBuilder.setConfTopic(JSON.stringify(contentTopics))
      ConferenceUpdateBuilder.setMembers(members)
      RL_MEET.ConferenceUpdate(
        ConferenceUpdateBuilder,
        (response) => {
          console.log(response)
          if (response.statusCode == '000000') {
            this.$message({
              message: '编辑会议成功',
              type: 'success',
            })
            this.openMeetingInfo(this.meetingInfoId)
            this.getMeetingList()
            this.resetData()
          } else {
            this.$message({
              message:
                response.data.statusMsg +
                this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          if (err.statusCode == '814028') {
            that.conidpop = err.confId
            that.confirmleave = true
          } else {
            that.$message({
              message: err.statusMsg,
              type: 'warning',
            })
          }
        }
      )
    },
    checkConnect(confId) {
      this.$bus.emit('leaveAllMeet', true)
      this.JoinMeet(confId)
    },
    JoinMeet(confId) {
      console.log(confId)
      console.log('isCloseVideo==============', this.isCloseVideo)
      console.log('isCloseAudio==============', this.isCloseAudio)
      let joinState = {
        joinMeetingState: 0,
        isCloseAudio: this.isCloseAudio,
        isCloseVideo: this.isCloseVideo,
      }
      if (!this.isCloseAudio) {
        joinState.joinMeetingState = joinState.joinMeetingState | 0x000040
      }

      if (!this.isCloseVideo) {
        joinState.joinMeetingState = joinState.joinMeetingState | 0x000004
      }
      if (!confId) {
        this.$message({
          message:
            this.$t('meeting.pleaseEnter') + this.$t('meeting.meetingNumber'),
          type: 'error',
        })
        return
      }
      let self = this
      let GetMeetInfoBuilder = new RL_MEET.GetMeetInfoBuilder()
      GetMeetInfoBuilder.setConfId(confId)
      GetMeetInfoBuilder.setHistoryConf(2)
      RL_MEET.GetMeetInfo(
        GetMeetInfoBuilder,
        // conferenceMsgBuilder,
        function (response) {
          var resp = JSON.parse(response.result)
          if (resp.statusCode == '000000') {
            console.log('resp==================', resp)
            if (resp.historyConf == 0) {
              if ((resp.state & 0x0200) == 512) {
                if (!resp.password) {
                  self
                    .$prompt(
                      '',
                      this.$t('meeting.pleaseEnter') +
                        this.$t('meeting.meetingPassword'),
                      {
                        confirmButtonText: this.$t('btn.determine'),
                        center: true,
                        showCancelButton: false,
                        inputPattern: /^\d{6}$/,
                        inputErrorMessage: this.$t(
                          'meeting.pleaseEnterPassword'
                        ),
                        inputType: 'number',
                        inputValue: this.$t('meeting.pleaseEnterPassword'),
                        inputPlaceholder: this.$t(
                          'meeting.pleaseEnterPassword'
                        ),
                      }
                    )
                    .then(({ value }) => {
                      if (value) {
                        self.$router.push({
                          path: '/yuntongxun/meeting',
                          query: { confId },
                        })
                      } else {
                        self.$message({
                          message: this.$t('meeting.pleaseInputPassword'),
                          type: 'error',
                        })
                      }
                    })
                    .catch(function () {})
                } else {
                  self.$router.push({
                    path: '/yuntongxun/meeting',
                    query: { confId },
                    params: { joinState },
                  })
                }
              } else {
                self.$router.push({
                  path: '/yuntongxun/meeting',
                  query: { confId },
                  params: { joinState },
                })
              }
            } else {
              self.$message.error('会议已结束')
            }
          } else if (resp.statusCode == '814006') {
            self.$message.error(self.$t('meeting.wrongMeetingNumber')) //会议号错误
          } else if (resp.statusCode == '814026') {
            self.$message.error(self.$t('meeting.meetingNotStart')) //会议未开始
          } else if (resp.statusCode == '814012') {
            self.$message.error(self.$t('meeting.meetingLocked')) //会议已锁定
          } else {
            self.$message({
              message:
                resp.statusMsg +
                ';' +
                self.$t('meeting.errorCode') +
                '：' +
                resp.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          console.log(err)
        }
      )
    },
    editReserveMeeting(data) {
      console.log(data)
      this.resetData()
      this.isEditReserveMeeting = true
      this.isShowMainpage = true
      this.isShowMeetingInfo = false
      this.activeMenu = 2
      this.StarMeetTitle = data.confName
      this.confStartDate = data.reserveStartTime.substring(0, 10)
      this.confStartTime = data.reserveStartTime.substring(11, 16)
      this.SelectTimeHour = this.SelectTimeHourList[
        parseInt(data.duration / 60)
      ].value
      this.SelectTimeMin = this.SelectTimeMinList[
        parseInt(data.duration % 60) / 15
      ].value
      this.memberCount = data.maxMember
      this.meetingTopicsList = [...data.topics]

      this.membersList = data.members.filter((mem) => {
        return mem.roleId != 1
      })
      this.meetingMembersList = this.membersList
      if (data.password) {
        this.needPassword = true
        this.meetpwd = data.password
      }
    },
    closeModal() {
      this.isShowShadow = false
      this.isShowEditTopic = false
      this.isShowSelectLiveModel = false
      this.$bus.emit('addPerson', false)
    },
    changeStartDate() {
      console.log(this.confStartDate)
      if (
        this.confStartDate ==
        this.getFormatDate(new Date(), false).substring(0, 10)
      ) {
        this.timePickerOptions.selectableRange =
          this.getFormatDate(new Date(), false).substring(11, 19) +
          ' - 23:59:59'
      } else {
        this.timePickerOptions.selectableRange = '00:00:00 - 23:59:59'
      }
    },
    datePickerBlur(e) {
      console.log(e)
    },
    selectTimeChangeIs00(Hour) {
      if (Hour == '0' && this.SelectTimeMin == '0') {
        this.SelectTimeMin = '15'
      }
    },
    selectTimeChangeIs001(min) {
      if (min == '0' && this.SelectTimeHour == '0') {
        this.SelectTimeMin = '15'
      }
    },
    setMemberCount() {
      this.memberCount = this.memberCount.replace(/[^\d]/g, '')
      if (
        this.canSetMemberCount &&
        Number(this.memberCount) > Number(this.meetMaxCount)
      ) {
        this.$message({
          message:
            this.$t('meeting.currentlyTheAvailabel') + `${this.meetMaxCount}`,
          type: 'error',
        })
        this.memberCount = this.meetMaxCount
      }
    },
    openMeetingInfo(confId) {
      this.isShowMainpage = false
      this.isShowMeetingInfo = true
      this.isShowHistoryMeetingInfo = false
      this.isMeetingEnd = false
      this.isEditReserveMeeting = false
      //获取会议详情
      // this.getMeetInfo(item.confId,0)
      this.meetingInfoId = confId
      this.historyConf = 0
      this.$refs.meetingDetail.getMeetInfo(confId, 0)
    },
    openHistoryMeetingInfo(item) {
      console.log(item)
      this.isShowMainpage = false
      this.isShowMeetingInfo = false
      this.meetingInfoId = item.confId
      this.isShowHistoryMeetingInfo = true
      this.isEditReserveMeeting = false
      // setTimeout(()=>{
      // 	console.log(this.$refs.meetingDetail)
      // },1000)
      // this.getMeetInfo(item.confId,1)
      this.historyConf = 1
      console.log(this.$refs.meetingDetail)
      this.$refs.historyMeetingDetail.getMeetInfo(item.confId, 1)
      let data = {
        confId: item.confId,
        historyConf: 1,
        sessionId: 'all',
      }
      this.$refs.historyMeetingDetail.getRecordList(data)
      // this.$refs.historyMeetingDetail.getAbstractList(data);  //获取摘要
    },
    cancelMeetingInfo(research) {
      this.resetData()
      this.isShowMainpage = true
      this.isShowMeetingInfo = false
      this.isShowHistoryMeetingInfo = false
      this.meetingTopicsList = []
      this.dynamicValidateForm = {
        topics: [
          {
            value: '',
          },
        ],
      }
      this.membersList = []
      this.meetingInfoModel = {}
      if (research) {
        this.meetingHistoryList = []
        this.reserveMeetingList = []
        this.startMeetingList = []
        this.getMeetingList()
        this.getHistoryMeetingList(0)
      }
    },
    closeMeeting(confId) {
      let that = this
      console.log(confId)
      RL_MEET.DeleteMeeting(
        confId,
        (response) => {
          console.log(response)
          let result = JSON.parse(response.result)
          if (result.statusCode == '000000') {
            that.cancelMeetingInfo()
          } else {
            this.$message({
              message:
                result.statusMsg +
                this.$t('meeting.errorCode') +
                result.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          that.$message({
            message: err.statusMsg,
            type: 'warning',
          })
        }
      )
    },
    openEditModal() {
      let that = this
      this.isShowShadow = true
      this.isShowEditTopic = true
      if (this.meetingTopicsList.length > 0) {
        that.dynamicValidateForm.topics = []
        that.meetingTopicsList.forEach((item, index) => {
          that.dynamicValidateForm.topics.push({
            value: item.value,
            key: index,
          })
        })
      } else {
        this.dynamicValidateForm = {
          topics: [
            {
              value: '',
            },
          ],
        }
      }
    },
    submitForm(formName) {
      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.dynamicValidateForm)
          that.meetingTopicsList = [...that.dynamicValidateForm.topics]
          console.log(
            'that.meetingTopicsList====================',
            that.meetingTopicsList
          )
          console.log(
            'this.meetingInfoModel=======================',
            this.meetingInfoModel
          )
          if (this.meetingInfoModel.confId) {
            this.updateMeetingTopic(this.meetingInfoModel)
          }
          that.closeModal()
          if (that.dynamicValidateForm.topics.length == 0) {
            that.dynamicValidateForm.topics.push({
              value: '',
              key: Date.now(),
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelTopic() {
      let that = this
      that.closeModal()
      if (that.dynamicValidateForm.topics.length == 0) {
        that.dynamicValidateForm.topics.push({
          value: '',
          key: Date.now(),
        })
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    removeTopic(item) {
      var index = this.dynamicValidateForm.topics.indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm.topics.splice(index, 1)
      }
    },
    addTopic() {
      if (this.dynamicValidateForm.topics.length >= 3) {
        this.$message({
          type: 'warning',
          message: '最多编辑三条议题',
        })
      } else {
        this.dynamicValidateForm.topics.push({
          value: '',
          key: Date.now(),
        })
      }
    },
    selectMemeberCallBack(selectList) {
      console.log('selectMemeberCallBack', selectList)
      this.membersList = selectList
      this.meetingMembersList = [...selectList]
      console.log(this.membersList)
      this.closeModal()
    },
    copyConfId() {
      let oInput = document.createElement('input')
      oInput.value = this.meetingInfoModel.confId
      document.body.appendChild(oInput)
      oInput.select() // 选择对象;
      console.log(oInput.value)
      document.execCommand('Copy') // 执行浏览器复制命令
      this.$message({
        message: '已成功复制到剪切板',
        type: 'success',
      })
      oInput.remove()
    },
    //获取近期会议列表
    getMeetingList() {
      let self = this
      this.startMeetingList = []
      this.reserveMeetingList = []
      let getOrderMeetListBuilder = new RL_MEET.GetOrderMeetListBuilder()
      getOrderMeetListBuilder.setSearchByMember(3)
      getOrderMeetListBuilder.setMemberId(self.LoginServe.UserId)
      getOrderMeetListBuilder.setMemberIdType(2)
      // getOrderMeetListBuilder.setStateInProcess(type)
      console.log(getOrderMeetListBuilder)
      RL_MEET.GetOrderMeetList(
        getOrderMeetListBuilder,
        (response) => {
          console.log('获取近期会议列表===', response)
          if (response.statusCode == '000000') {
            if (response.confs && response.confs.length > 0) {
              let meetingList = response.confs
              meetingList.forEach((meet) => {
                let holder = meet.members.filter((member) => {
                  return member.roleId == 1
                })
                console.log(holder)
                meet.holder = holder[0] ? holder[0].userName : ''
                if (meet.state == 0) {
                  self.reserveMeetingList.push(meet)
                } else {
                  self.startMeetingList.push(meet)
                }
              })
            } else {
              this.startMeetingList = []
              this.reserveMeetingList = []
            }
          } else {
            this.$message({
              message:
                response.data.statusMsg +
                this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          console.log(err)
          if (err.statusCode == '814028') {
            self.conidpop = err.confId
            self.confirmleave = true
          } else if (err.code == 170003) {
          } else {
            self.$message({
              message: err.msg,
              type: 'warning',
            })
          }
        }
      )
    },
    historyLoad() {
      this.historyPageNo++
      this.loadingHistory = true
      this.getHistoryMeetingList(this.historyPageNo)
    },
    //获取历史会议列表
    getHistoryMeetingList(page) {
      console.log('page=====================', page)
      let that = this
      // this.meetingHistoryList=[];
      this.loadingHistory = true
      let GetHistoryRoomBuilder = new RL_MEET.GetHistoryRoomBuilder()
      GetHistoryRoomBuilder.setSearchByMember(3)
      GetHistoryRoomBuilder.setMemberId(this.LoginServe.UserId)
      GetHistoryRoomBuilder.setMemberIdType(2)
      GetHistoryRoomBuilder.setPageNo(page)
      RL_MEET.GetHistoryRoom(
        GetHistoryRoomBuilder,
        (response) => {
          console.log('historyMeeting===============================', response)
          this.loadingHistory = false
          if (response.code == 200) {
            let result = JSON.parse(response.result)
            console.log('historyResult===========', result)
            if (result.confs && result.confs.length > 0) {
              this.noMoreHistoryList = false
              let meetingList = [...result.confs]
              meetingList.forEach((meet) => {
                let holder = meet.members.filter((member) => {
                  return member.roleId == 1
                })
                meet.holder = holder[0] ? holder[0].userName : ''
                meet.startTime = meet.startTime
                  ? meet.startTime
                  : meet.reserveStartTime
                that.meetingHistoryList.push(meet)
              })
            } else {
              this.noMoreHistoryList = true
            }
          }
        },
        function (err) {
          console.log(err)
          if (err.statusCode == '814028') {
            that.conidpop = err.confId
            that.confirmleave = true
          } else if (err.code == 170003) {
          } else {
            that.$message({
              message: err.msg,
              type: 'warning',
            })
          }
        }
      )
    },
    //获取会议信息
    getMeetInfo(confId, historyConf) {
      let GetMeetInfoBuilder = new RL_MEET.GetMeetInfoBuilder()
      GetMeetInfoBuilder.setConfId(confId)
      GetMeetInfoBuilder.setHistoryConf(historyConf)
      RL_MEET.GetMeetInfo(
        GetMeetInfoBuilder,
        (response) => {
          console.log(response)
          if (response.code == '200') {
            let result = JSON.parse(response.result)
            console.log(result)
            this.meetingTopicsList = []
            console.log(this.meetingTopicsList)
            result.members.forEach((m) => {
              if (m.deviceType) {
                var idx = m.memberId.indexOf('@')
                m.memberId = idx > 0 ? m.memberId.substring(0, idx) : m.memberId
              }
              m.account = m.memberId
              if (m.roleId == 1) {
                result.holder = m.userName
              }
            })
            console.log(result.members)
            console.log(this.meetingInfoModel)
            let topics = JSON.parse(result.confTopic).topics
            topics.forEach((item) => {
              item.value = item.value ? item.value : item.topic
            })
            result.topics = [...topics]
            this.meetingInfoModel = result
            this.meetingTopicsList = [...topics]
          } else {
            this.$message({
              message:
                response.data.statusMsg +
                this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          if (err.statusCode == '814028') {
            that.conidpop = err.confId
            that.confirmleave = true
          } else {
            that.$message({
              message: err.statusMsg,
              type: 'warning',
            })
          }
        }
      )
    },
    //更新会议议题
    updateMeetingTopic(meet) {
      let that = this
      console.log(meet)
      let contentTopics = {
        topics: this.meetingTopicsList,
      }
      console.log(contentTopics)
      let ConferenceUpdateBuilder = new RL_MEET.ConferenceUpdateBuilder()
      ConferenceUpdateBuilder.setConfId(meet.confId)
      ConferenceUpdateBuilder.setConfTopic(JSON.stringify(contentTopics))
      RL_MEET.ConferenceUpdate(
        ConferenceUpdateBuilder,
        (response) => {
          console.log(response)
          if (response.statusCode == '000000') {
          } else {
            that.getMeetInfo(meet.confId, 0)
            this.$message({
              message:
                response.data.statusMsg +
                this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: 'error',
            })
          }
        },
        function (err) {
          that.getMeetInfo(meet.confId, 0)
          if (err.statusCode == '814028') {
            that.conidpop = err.confId
            that.confirmleave = true
          } else {
            that.$message({
              message: err.statusMsg,
              type: 'warning',
            })
          }
        }
      )
    },
    inviteLive() {
      if (this.membersList.length == 0) {
        this.$message({ message: '请先选择参会人', type: 'error' })
        return false
      }
      this.meetingMembersList = []
      this.disabledList = [...this.membersList]
      this.isShowSelectLiveModel = true
    },
    updateMeetingList() {
      this.isShowMainpage = true
      this.noMoreHistoryList = false
      this.loadingHistory = false
      this.startMeetingList = []
      this.reserveMeetingList = []
      this.meetingHistoryList = []
      this.getMeetingList()
      this.getHistoryMeetingList(0)
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.resetData()
      vm.activeMenu = 1
    })
  },
  beforeRouteLeave(to, from, next) {
    if (this.$refs.dataPicker) this.$refs.dataPicker.hidePicker()
    if (this.$refs.timePicker) this.$refs.timePicker.hidePicker()
    next()
  },
}
