              max-height: Vh(160);
              // max-width: 85%;
              li {
                float: left;
                margin-right: Vw(40);
                span {
                  display: block;
                  margin: 0 auto Vh(10);
                  width: Vh(51);
                  height: Vh(51);
                  border-radius: 50%;
                  text-align: center;
                  line-height: Vh(51);
                  font-size: Vh(20);
                }
                img {
                  display: block;
                  margin: 0 auto Vh(10);
                  width: Vh(51);
                  height: Vh(51);
                  border-radius: 50%;
                }
                p {
                  line-height: Vh(21);
                  font-size: Vh(16);
                  color: #000000;
                  text-align: center;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  width: Vw(70);
                }
              }
            }
          }
        }
      }
      div.con.info {
        width: Vw(660);
        // height: 100%;
        padding: Vh(30) Vw(30) Vh(80);
        box-sizing: border-box;
        .basicInfoCard {
          li {
            span {
              font-size: Vh(20);
              font-weight: 400;
              color: #000000;
            }
          }
        }
        .topicCard {
          margin-top: 10px;
          p {
            font-size: Vh(20);
            font-weight: 700;
            color: #000000;
            margin-bottom: Vh(8);
            span {
              font-size: Vh(14);
              color: #4f98ff;
              cursor: pointer;
              margin-left: Vh(20);
            }
            img {
              width: Vh(18);
              height: Vh(18);
              vertical-align: bottom;
              cursor: pointer;
            }
          }
          ul {
            padding: 5px 15px;
            max-height: 5vh;
            overflow: auto;
            li {
              // height: Vh(29);
              // margin-bottom: Vh(9);
              opacity: 0.7;
              font-size: Vh(18);
              color: #000000;
              line-height: Vh(26);
            }
            p {
              width: 100%;
              line-height: 5vh;
              text-align: center;
              opacity: 0.4;
              font-size: Vh(22);
            }
          }
        }
        .memberCard {
          margin-top: 3px;
          .tit {
            height: Vh(35);
            margin-bottom: Vh(10);
            span.flt {
              font-size: Vh(20);
              font-weight: 700;
              color: #000000;
              line-height: Vh(35);
            }
            img {
              height: Vh(32);
              width: auto;
              margin-left: Vw(7);
              margin-top: Vh(1.5);
              cursor: pointer;
            }
          }
          ul {
            max-height: 8vh;
            overflow: auto;
            li {
              float: left;
              margin-right: Vw(20);
              margin-bottom: Vh(10);
              span {
                display: block;
                margin: 0 auto Vh(10);
                width: Vh(45);
                height: Vh(45);
                border-radius: 50%;
                text-align: center;
                line-height: Vh(45);
                font-size: Vh(20);
              }
              img {
                display: block;
                margin: 0 auto Vh(10);
                width: Vh(45);
                height: Vh(45);
                border-radius: 50%;
              }
              p {
                line-height: Vh(21);
                font-size: Vh(16);
                color: #000000;
                text-align: center;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                width: Vw(70);
              }
            }
          }
        }
        .videoCard {
          margin-top: 3px;
          .tit {
            height: Vh(35);
            margin-bottom: Vh(10);
            span.flt {
              font-size: Vh(20);
              font-weight: 700;
              color: #000000;
              line-height: Vh(35);
            }
          }
          .radioUl {
            span {
              cursor: pointer;
            }
          }
          .videoUl {
            display: flex;
            flex-wrap: wrap;
            max-height: Vh(130);
            overflow: auto;
            li {
              margin-left: Vw(10);
              #video {
                width: Vw(200);
                height: Vh(130);
              }
              span {
                font-size: Vh(14);
                color: #4f98ff;
                cursor: pointer;
                // margin-left: Vh(20);
              }
            }
          }
        }
        .discussCard {
          margin-top: 3px;
          .tit {
            height: Vh(35);
            span.flt {
              font-size: Vh(20);
              font-weight: 700;
              color: #000000;
              line-height: Vh(35);
            }
          }
          p {
            margin-top: 5px;
          }
          p:nth-child(4) {
            overflow: hidden; //将超出的部分隐藏
            text-overflow: ellipsis; //超出部分用省略号显示
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
          }
          p:nth-child(5) {
            text-align: right;
            font-size: Vh(14);
            color: #4f98ff;
            cursor: pointer;
          }
        }
        .btns {
          padding: 0 Vh(100);
          margin-top: Vh(20);
        }
      }
    }
    // .meetingTitle{
    // 	width: 100%;
    //   height:105px;
    //   margin-bottom: 10px;
    // 	.title{
    // 		height: 55px;
    // 		font-size: 42px;
    // 		font-weight: 700;
    // 		text-align: left;
    // 		color: #ffffff;
    // 	}
    // 	.meetingStatus{
    // 		margin-left: 20px;
    // 	}
    // }
    .meetinfo {
      width: 100%;
      height: 100%;
      margin-bottom: 10px;
      // .meetingTitle{
      // 	height: 75px;
      // 	padding: 0;
      // 	.mainTit{
      // 		display: flex;
      // 		justify-content: center;
      // 		.title{
      // 			height: 55px;
      // 			font-size: 42px;
      // 			font-weight: 700;
      // 			text-align: center;
      // 			color: #ffffff;
      // 			max-width: 60%;
      // 			overflow: hidden;
      // 			text-overflow:ellipsis;
      // 			white-space: nowrap;
      // 		}
      // 		.meetingStatus{
      // 			margin-left: 20px;
      // 			padding-top: 20px;
      // 		}
      // 	}

      // }
      .right-main {
        height: calc(100% - 75px);
      }
    }
  }
  .editTopicModal {
    width: 100%;
    max-width: 600px;
    max-height: 50vh;
    overflow: auto;
    padding: 20px;
    background-color: #fff;
    font-size: 14rem;
    border: solid 1px #ccc;
    span.frt {
      padding: 0 Vw(20);
      line-height: Vh(40);
      border: 1px solid #2570ff;
      border-radius: Vh(11);
      font-size: Vh(12);
      color: rgba(0, 0, 0, 0.8);
      text-align: center;
      cursor: pointer;
    }
    .topicBottom {
      margin-top: Vh(50);
      display: flex;
      justify-content: center;
      span {
        padding: 0 Vw(20);
        line-height: Vh(40);
        border: 1px solid #2570ff;
        border-radius: Vh(11);
        font-size: Vh(12);
        color: rgba(0, 0, 0, 0.8);
        text-align: center;
        cursor: pointer;
        margin-right: Vw(20);
      }
      .confirm {
        background-color: #367ee8;
        color: #fff;
      }
    }
  }
  ::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }
}
</style>

<style lang="scss">
@function Vh($col) {
  @return ($col/1080) * 100vh;
}
@function Vw($col) {
  @return ($col/1920) * 100vw;
}
.el-card__body {
  padding: 10px;
}
.CloudMeet_New {
  .el-checkbox__input {
    float: left;
    position: relative;
    width: Vh(14);
    height: Vh(14);
    margin-top: Vh(8);
  }
  .start {
    .item {
      li.password {
        .el-switch {
          font-size: Vh(15);
          line-height: Vh(40);
          float: right;
          position: absolute;
          top: 50%;
          margin-top: -10px;
          right: 0;
          .el-switch__core {
            line-height: Vh(40);
          }
        }
        .el-switch.is-checked .el-switch__core {
          border-color: #2570ff;
          background-color: #2570ff;
        }
      }
      li.confName,
      li.max-member,
      .pwd {
        .el-input--small {
          .el-input__inner {
            width: 100%;
            height: Vh(29);
            line-height: Vh(29);
            opacity: 0.4;
            font-size: Vh(16);
            text-align: right;
            color: #000000;
            border: none;
            padding: 0;
          }
        }
      }
      li.confStartTime {
        .el-date-editor--time.el-date-editor.el-input {
          width: Vw(100);
        }
        .date_picker .el-picker__footer .el-button--text {
          display: none;
        }
        .el-date-editor.el-input {
          width: Vw(160);
          .el-input__inner {
            width: 100%;
            padding-left: Vw(30);
            padding-right: Vw(10);
            font-size: Vh(13);
            text-align: left;
            height: Vh(29);
            line-height: Vh(29);
          }
          .el-input__prefix {
            left: Vw(8);
            .el-input__icon {
              width: Vw(20);
              line-height: Vh(29);
            }
          }
        }
      }
      .pwd {
        .el-input--small {
          float: left;
          width: Vw(150);
          .el-input__inner {
            text-align: left;
            width: Vw(180);
          }
        }
      }
      li.confTime {
        overflow: hidden;
        .el-select {
          float: left;
          width: Vw(97) !important;
          margin-left: Vw(20);
          .el-input--small {
            width: Vw(97);
            .el-input__inner {
              font-size: Vh(13);
              padding: 0 0 0 Vw(10);
              text-align: left;
              height: Vh(29);
              line-height: Vh(29);
            }
            .el-input__suffix {
              right: 0;
              .el-select__caret {
                height: Vh(29);
                line-height: Vh(29);
                font-size: Vh(14);
              }
            }
          }
        }
      }
    }
  }
  .btns {
    .el-button {
      width: Vw(138);
      height: Vh(54);
      border-radius: Vh(5);
      padding: 0;
      font-size: Vh(20);
    }
    .cancel {
      color: #2570ff;
    }
    .cancel:hover {
      color: #2570ff;
      background: #fff;
    }
  }
  .join {
    .el-input--small {
      display: block;
      .el-input__inner {
        line-height: Vh(60);
        height: Vh(60);
        font-size: Vh(18);
        padding-left: Vw(55);
        color: rgba(112, 112, 112, 0.64);
        border-color: rgba(112, 112, 112, 0.64);
      }
      .el-input__icon {
        font-size: Vh(24);
        width: Vw(55);
        color: rgba(33, 30, 30, 0.57);
      }
    }
  }
  .meetingTitle {
    .el-tag--dark.el-tag--success {
      background-color: #67c23a;
      border-color: #67c23a;
      color: #fff;
    }
  }
}
</style>



