<template>
  <div style="height:100%" class="meetinfo">
    <div class="meetingTitle">
        <div class="mainTit">
          <span class="title">{{meetingInfoModel.confName}}</span>	
          <span class="meetingStatus" ><el-tag  type="info" effect="dark" size="mini">已结束</el-tag></span>
        </div>					
    </div>
    <div class="right-main">
      <div class="con info" v-if="!isShowhistoryMsg">
        <el-card shadow="always" class="basicInfoCard">
          <ul>
            <li style="margin-bottom:10px">
              <span>会议号：<span>{{meetingInfoModel.confId}}</span><i class="el-icon-document-copy" style="color:#4f98ff" @click="copyConfId"></i></span>
              <span style="float:right">创建者：{{meetingInfoModel.holder}}</span>
            </li>
            <li>
              <span>时间：{{meetingInfoModel.state?meetingInfoModel.startTime:meetingInfoModel.reserveStartTime}}</span>
              <span style="float:right">会议密码：{{meetingInfoModel.password?meetingInfoModel.password:'无'}}</span>
            </li>
          </ul>
        </el-card>
        <el-card shadow="always" class="topicCardHistory" style="margin-top:3px">
          <p>会议议题<span v-if="LoginServe.UserId==meetingInfoModel.holderAccount" @click="openEditModal">编辑<img src="../../assets/img/meet/start-edit.png" alt=""></span></p>
          <ul v-if="meetingTopicsList&&meetingTopicsList.length>0">
            <li v-for="(item,index) in meetingTopicsList " :key="index">
              <span>{{index+1+'.'}}</span> {{item.value}}
            </li>
          </ul>
          <ul v-else>						
            <p style="">{{LoginServe.UserId==meetingInfoModel.holderAccount?'您可以添加会议议题!':'暂无会议议题'}}</p>
          </ul>
        </el-card>
        <el-card shadow="always" class="memberCard" style="margin-top:3px">
          <div class="tit">
            <span class="flt">参会人 ({{membersList.length}})</span>
            <img v-if="LoginServe.UserId==meetingInfoModel.holderAccount" src="../../assets/img/meet/start-add.png" alt="" class="flt" @click.prevent="inviteMember">
            <!-- <img src="../../assets/img/meet/start-del.png" alt="" class="flt"> -->
          </div>
          <ul>
            <li v-for="(member,index) in membersList" :key="index">
              <span v-if="!member.photourl" :style="{backgroundImage:getcolor(member.account)}">{{member.userName || member.username | nameslice}}</span>
              <img v-else :src="member.photourl" alt="">
              <p>{{member.userName||member.username}}</p>
            </li>
          </ul>
        </el-card>
        <!-- <el-card shadow="always" class="videoCard" v-if="!isMeetingEnd">
          <div class="tit">
            <span class="flt">云录像</span>
          </div>
          <ul class="radioUl">
            <el-radio-group v-model="radio">
              <el-radio :label="3">主持人视角</el-radio>
              <el-radio :label="6">参会人视角</el-radio>
            </el-radio-group>
            <span style="float:right">选择录像源<i class="el-icon-video-camera-solid" style="margin-left:10px"></i></span>
          </ul>		
        </el-card> -->
        <el-card shadow="always" class="discussCard" >
          <div class="tit">
            <span class="flt">会中讨论</span>
          </div>
          <div class="discussMember">
            <span @click="checkImMsg">全员讨论</span>
          </div>
          <!-- <p class="">会议纪要</p>
          <p class="discussContent">{{abstractContent}}</p>
          <p @click="isShowAbstract = true">编辑</p> -->
        </el-card>
        <el-card shadow="always" class="videoCard"  >
          <div class="tit">
            <span class="flt">云录像</span>
          </div>
          <ul class="videoUl">
            <!-- <li>
              <video id="video"  controls  data-index="1" class="window_img" type="video/mp4" src="http://opm.yuntongxun.com/edm/edm20200610/images/46d0bee8-bb73-11ea-a9e7-7cd30aeb7804.mp4"></video>
              <span> <i class="el-icon-download"></i>下载</span>
            </li> -->
            <li v-for="(item,index) in recordFileList" :key='index'>
              <video id="video"  controls  data-index="1" class="window_img" type="video/mp4" :src="item.mp4.downloadUrl"></video>
              <span @click="downRecord(item)"> <i class="el-icon-download"></i>下载</span>
            </li>
          </ul>
        </el-card>
        <div>
          <div class="btns" v-if="LoginServe.UserId==meetingInfoModel.holderAccount">
            <el-button class="cancel flt" @click="cancelMeetingInfo">返回</el-button>
            <el-button type="primary" class="frt" @click="reStartMeeting">重新开会</el-button>
          </div>
          <div class="btns" style="display:flex;justify-content: center;" v-else><el-button  class="cancel" @click="cancelMeetingInfo">返回</el-button></div>
        </div>
      </div>
      <div class="con info" v-else>
        <p class="msgBack" @click="isShowhistoryMsg=false">返回</p>
      </div>
    </div>
    <div class="shadow" v-if="isShowEditTopic">
      <div class="editTopicModal">
        <!-- <span class="frt" @click="submitForm('dynamicValidateForm',historyConf)">完成</span>		 -->
          <ul style="margin-top:50px">
            <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
              <el-form-item
                v-for="(topic, index) in dynamicValidateForm.topics"
                :label="index+1+''"
                :key="topic.key"
                :prop="'topics.' + index + '.value'"
                :rules="{required: true, message: '会议议题不能为空', trigger: 'blur'}"
              >
                <el-input v-model="topic.value" style="width:80%" maxlength="40"></el-input> <el-button icon="el-icon-delete" circle @click.prevent="removeTopic(topic)"></el-button>
              </el-form-item>
            </el-form>
          </ul>		
        <span class="frt" @click="addTopic">添加</span>		
        <div class="topicBottom">
          <span  @click="cancelTopic">取消</span>
          <span  @click="submitForm('dynamicValidateForm',historyConf)">完成</span>
        </div>			
      </div>
    </div>
    <el-dialog
      title="会议纪要"
      :visible.sync="isShowAbstract"
      width="30%">
      <el-input
        type="textarea"
        autosize
        placeholder="请输入内容"
        v-model="abstractContent">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isShowAbstract = false">返 回</el-button>
        <el-button type="primary" @click="updateAbstract">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {mapGetters,mapState} from 'vuex';
export default {
  props: ['confId','historyConf'],
  
  data(){
    return {
      meetingId:'',
      meetingTopicsList:[],
      membersList:[],
      editMemberList:[],
      radio:3,
      isShowEditTopic:false,
      isShowAddMember:false,
      isShowhistoryMsg:false,
      meetingInfoModel:{},
      dynamicValidateForm: {
        topics: [{
          value: ''
        }],
      },
      holderObj:{},
      menuObj: {
        isCopeMsg: false, // 是否显示复制
        isShowFile: {
          bool: false, // 消息内是否包含文件
          isSaveMsg: false // 是否可另存
        },
        isForwardMsg: false, // 是否显示转发
        isReferenceMsg: false, // 是否可以引用
        isCollectMsg: false, // 是否显示收藏
        isGetBackMsg: false, // 是否显示撤回
        isMultiSelect: true, //是否显示多选
      },
      recordFileList:[],
      isShowAbstract:false,
      abstractContent:'',
      maxSelectMemberLength:0
    }
  },
  computed: {
    ...mapState(["LoginServe"]),
    ...mapGetters([
      'user_info',
      'user_account',
      'allMessages'
    ]),
  },
  watch:{
    confId:function(){
      console.log(this.confId)
    }
  },
  methods:{
    getMeetInfo(confId,historyConf){
      let that = this;
      this.isShowhistoryMsg = false;
      let GetMeetInfoBuilder = new RL_MEET.GetMeetInfoBuilder();	
      GetMeetInfoBuilder.setConfId(confId);
      GetMeetInfoBuilder.setHistoryConf(1);
      RL_MEET.GetMeetInfo(GetMeetInfoBuilder,
        response => {
          console.log(response)
          if (response.code == "200") {
            let result = JSON.parse(response.result)			
            console.log('RL_MEET.GetMeetInfo===', result)
            this.meetingTopicsList = []
            console.log(this.meetingTopicsList)
            result.members.forEach(m=>{					
              if(m.deviceType){
                var idx = m.memberId.indexOf('@')
                m.memberId = idx>0? m.memberId .substring(0,idx):m.memberId
              }
              m.account =  m.memberId;
              if(m.roleId==1){
                result.holder = m.userName;
                result.holderAccount = m.account;
              }
            })
            this.membersList = [...result.members]
            console.log(result.members)
            console.log(this.meetingInfoModel)
            let topics = result.confTopic?JSON.parse(result.confTopic).topics:[]
            topics.forEach(item=>{
              item.value = item.value?item.value:item.topic
            })
            result.topics = [...topics];
            this.meetingInfoModel = result;
            this.meetingTopicsList = [...topics];		
            this.maxSelectMemberLength = this.meetingInfoModel.maxMember - this.membersList.length
          } else {
            this.$message({
              message:
                response.data.statusMsg + this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: "error"
            });
          }
        },
        function(err) {
          if (err.statusCode == "814028") {
            // that.conidpop = err.confId;
            // that.confirmleave = true;
          } else {
            that.$message({
              message: err.statusMsg,
              type: "warning"
            });
          }
        }
      )
    },
    getRecordList(data){
      console.log(data)
      let that = this;
      RL_MEET.RecordGetFileList(data,
        response=>{
          console.log(response)
          let result = JSON.parse(response.result)
          console.log(result)
          if(result.statusCode=='000000'){
            if(result.recordData&&result.recordData.length>0){
              result.recordData.forEach((item)=>{
                item.mp4.downloadUrl = item.mp4.downloadUrl.replace(/(https?:\/\/)([0-9a-z._]+)(:[0-9]+)?\/(dvrFile\/)?/, window.dvrFileAddrs)
              })
              that.recordFileList = result.recordData
            }else{
              that.recordFileList = []
            }
          }
        },
        function(err) {
          console.log(err)
          that.$message({
            message: err.statusMsg,
            type: "warning"
          });
        }
      )
    },
    getAbstractList(data){
      console.log(data)
      let that = this;
      RL_MEET.GetSummaryList(data,
        response=>{
          console.log(response)
          let result = JSON.parse(response.result)
          console.log(result)
          if(result.statusCode=='000000'){
            if(result.confAbstracts&&result.confAbstracts.length>0){

            }else{
              
            }
          }
        },
        function(err) {
          console.log(err)
          that.$message({
            message: err.statusMsg,
            type: "warning"
          });
        }
      )
    },
    copyConfId(){
      let oInput = document.createElement('input');
      oInput.value = this.meetingInfoModel.confId;
      document.body.appendChild(oInput);
      oInput.select(); // 选择对象;
      console.log(oInput.value)
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message({
        message: '已成功复制到剪切板',
        type: 'success'
      });
      oInput.remove()
    },
    openEditModal(){
      let that = this;
      this.isShowEditTopic = true;
      if(this.meetingInfoModel.topics&&this.meetingInfoModel.topics.length>0){
					that.dynamicValidateForm.topics = []
					that.meetingTopicsList.forEach((item,index)=>{
						that.dynamicValidateForm.topics.push({value:item.value,key:index})
					})
				}else{
					this.dynamicValidateForm={
						topics: [{
							value: ''
						}],
        	}
				}
    },
	  inviteMember(){
      this.maxSelectMemberLength = this.meetingInfoModel.maxMember - this.membersList.length;
      console.log(this.membersList);
      this.$bus.emit('addPerson',true, this.maxSelectMemberLength, this.membersList, this.addMemeberCallBack, true)
      this.holderObj = this.membersList.find((item)=>{
        return item.roleId = 1
      })
      this.editMemberList = this.membersList.filter((mem)=>{
        return mem.roleId!=1
      })
    },
    addMemeberCallBack(selectList){
			console.log(selectList)
      this.membersList = [...selectList];
      // if(!!this.holderObj && this.holderObj instanceof Object){
      //   this.membersList.unshift(this.holderObj)
      // }
      this.$bus.emit('addPerson', false);
		},
    cancelMeetingInfo(){
      this.$emit('cancelMeetingInfo')
    },
    closeMeeting(){},
    JoinMeet(confId){
      console.log(confId)
      if (!confId) {
        this.$message({
          message: this.$t('meeting.pleaseEnter') + this.$t('meeting.meetingNumber'),
          type: "error"
        });
        return;
      }
      let self = this
      // sessionStorage.setItem("IsCheckedOpenAudio", this.IsCheckedOpenAudio);
      // sessionStorage.setItem("IsCheckedOpenVideo", this.IsCheckedOpenVideo);
      var conferenceMsgBuilder = new  RL_Media.ConferenceMsgBuilder();
      conferenceMsgBuilder.setPath("/REST/Conference/Info?source=SDK");
      conferenceMsgBuilder.setContent({
        appId: self.LoginServe.AppId,
        userId: self.LoginServe.UserId,
        confId: confId
      });
      RL_Media.ConferenceMsg(
        conferenceMsgBuilder,
        function(response) {
          var resp = JSON.parse(response.result);
          if (resp.statusCode == "000000") {
            if ((resp.state & 0x0200) == 512) {
              if (!resp.password) {
                self
                  .$prompt("", this.$t('meeting.pleaseEnter') + this.$t('meeting.meetingPassword'), {
                    confirmButtonText: this.$t('btn.determine'),
                    center: true,
                    showCancelButton: false,
                    inputPattern: /^\d{6}$/,
                    inputErrorMessage: this.$t('meeting.pleaseEnterPassword'),
                    inputType: "number",
                    inputValue: this.$t('meeting.pleaseEnterPassword'),
                    inputPlaceholder: this.$t('meeting.pleaseEnterPassword')
                    // maxlength:6,
                    // cancelButtonText: "取消",
                    // inputType: 'password',
                    // inputValidator: joinMeetPwd
                  })
                  .then(({ value }) => {
                    if (value) {
                      self.$router.push({
                        path: '/yuntongxun/meeting',
                        query: {confId}
                      })
                    } else {
                      self.$message({
                        message: this.$t('meeting.pleaseInputPassword'),
                        type: "error"
                      });
                    }
                  })
                  .catch(function() {});
              } else {
                self.$router.push({
                  path: '/yuntongxun/meeting',
                  query: {confId}
                })
              }
            } else {
              self.$router.push({
                path: '/yuntongxun/meeting',
                query: {confId}
              })
            }
          } else if (resp.statusCode == "814006") {
            self.$message.error(self.$t('meeting.wrongMeetingNumber'));//会议号错误
          } else if (resp.statusCode == "814026") {
            self.$message.error(self.$t('meeting.meetingNotStart'));//会议未开始
          } else if (resp.statusCode == "814012") {
            self.$message.error(self.$t('meeting.meetingLocked'));//会议已锁定
          } else {
            self.$message({
              message: resp.statusMsg + ";"+self.$t('meeting.errorCode')+"：" + resp.statusCode,
              type: "error"
            });
          }
        },
        function(err) {
          console.log(err);
        }
      );
    },
    submitForm(formName,historyConf){
      console.log('historyConf===========',historyConf)
      // this.isShowEditTopic = false;
      let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
						console.log(this.dynamicValidateForm)
						that.meetingTopicsList = [...that.dynamicValidateForm.topics]
						console.log('that.meetingTopicsList====================',	that.meetingTopicsList)
						console.log('this.meetingInfoModel=======================',this.meetingInfoModel)
						if(this.meetingInfoModel.confId){
							this.updateMeetingTopic(this.meetingInfoModel,historyConf)
						}
						// that.closeModal();
						if(that.dynamicValidateForm.topics.length==0){
							that.dynamicValidateForm.topics.push({
								value: '',
								key: Date.now()
							});
						}
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    cancelTopic(){
      let that = this;
      this.isShowEditTopic = false
      if(that.dynamicValidateForm.topics.length==0){
        that.dynamicValidateForm.topics.push({
          value: '',
          key: Date.now()
        });
      }
    },
    //更新会议议题
    updateMeetingTopic(meet,historyConf){
      let that = this;
      console.log(meet)
      let topics = [];
      this.meetingTopicsList.forEach(item=>{
        topics.push({
          topic:item.value
        })
      })
      let contentTopics = {
        topics
      }
      console.log(contentTopics)
      let HistoryConfTopicBuilder = new RL_MEET.HistoryConfTopicBuilder();	
      HistoryConfTopicBuilder.setConfId(meet.confId)
      HistoryConfTopicBuilder.setConfTopic(JSON.stringify(contentTopics))
      RL_MEET.historyConfTopicUpdate(
        HistoryConfTopicBuilder,
        response => {
          this.isShowEditTopic = false
          console.log(response)
          if (response.statusCode == "000000") {
            
          } else {
            // that.getMeetInfo(meet.confId,historyConf)
            this.$message({
              message:
                response.data.statusMsg + this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: "error"
            });
          }
        },
        function(err) {
          // that.getMeetInfo(meet.confId,historyConf)
          if (err.statusCode == "814028") {
            that.conidpop = err.confId;
            that.confirmleave = true;
          } else {
            that.$message({
              message: err.statusMsg,
              type: "warning"
            });
          }
        }
      );
    },
    removeTopic(item){
      var index = this.dynamicValidateForm.topics.indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm.topics.splice(index, 1)
      }		
    },
    addTopic(){
      if(this.dynamicValidateForm.topics.length>=3){
        this.$message({
          type:'warning',
          message:'最多编辑三条议题'
        })
      }else {
        this.dynamicValidateForm.topics.push({
          value: '',
          key: Date.now()
        });
      }
    },
    //获取会中消息
   async checkImMsg(){
      this.isShowhistoryMsg = true;
      console.log('chatGroupId=================', this.meetingInfoModel.chatGroupId)
      let obj = {
        talker:this.meetingInfoModel.chatGroupId,
        pageSize:  20,
        msgDecompression: 1,
        username: this.user_info.username,
      }
      // let requestRes = await reqHistoryMsg(obj)
      // console.log(requestRes)

      this.$bus.emit("firstGetHistory", this.meetingInfoModel.chatGroupId);
    },
    reStartMeeting(){
      console.log(this.membersList)
      console.log(this.meetingTopicsList)
      let that = this;
      let createMeetbuilder = new RL_MEET.CreateMeetBuilder();
      if(this.meetingTopicsList){
        let topics = [];
        this.meetingTopicsList.forEach(item=>{
          topics.push({
            topic:item.value
          })
        })
        let contentTopics = {
          topics
        }
        createMeetbuilder.setConfTopic(JSON.stringify(contentTopics));
      }
      if(this.meetingInfoModel.password){
         createMeetbuilder.setPassword(this.meetingInfoModel.password);
      }
      createMeetbuilder.setConfName( `${this.LoginServe.Profile.NickName}` + this.$t('meeting.sMeeting'));
      let startTime = '';
      startTime = this.getFormatDate(new Date(),false)
      console.log(startTime)
      createMeetbuilder.setStartTime(startTime)
      createMeetbuilder.setSendReserveNote(1)//是否给会议成员发送提醒通知（提醒成员被列入预约会议成员）
      createMeetbuilder.setChatInConf(1);
      createMeetbuilder.setVoiceMode(1);
      // // createMeetbuilder.setJoinState(joinstate);
      createMeetbuilder.setMaxMember(20);
      createMeetbuilder.setConfType(0);
      createMeetbuilder.setMembers(this.membersList);
      createMeetbuilder.setAutoRecord(1);
      createMeetbuilder.setRecordAction(40);
      createMeetbuilder.setAutoClose(0);
      console.log('createMeetbuilder=======================',createMeetbuilder)
      RL_MEET.CreateMeet(
        createMeetbuilder,
        response => {
          console.log('createMeetbuilder====',response)
          // console.log(e, 'yuyuyuyuyu')
          // that.$store.commit("SET_CREATER", true);
          if (response.statusCode == "000000") {						
              this.$router.push({
                path: '/yuntongxun/meeting',
                query: {confId:response.confId}
              })		
          } else {
            this.$message({
              message:
                response.data.statusMsg + this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: "error"
            });
          }
        },
        function(err) {
          if (err.statusCode == "814028") {
            that.conidpop = err.confId;
            that.confirmleave = true;
          } else {
            that.$message({
              message: err.statusMsg,
              type: "warning"
            });
          }
        }
      );
    },
    getFormatDate: function (now,push) {
      now = now || new Date();				
      var	nowDate = push?now.getTime() + 1000 * 60 * 15: now; //预约会议推迟15分钟
      nowDate = new Date(nowDate);
      var year = nowDate.getFullYear();
      var month =
        nowDate.getMonth() + 1 < 10 ?
        "0" + (nowDate.getMonth() + 1) :
        nowDate.getMonth() + 1;
      var date =
        nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
      var hour =
        nowDate.getHours() < 10 ? "0" + nowDate.getHours() : nowDate.getHours();
      var minute =
        nowDate.getMinutes() < 10 ?
        "0" + nowDate.getMinutes() :
        nowDate.getMinutes();
      var seconds =
        nowDate.getSeconds() < 10 ?
        "0" + nowDate.getSeconds() :
        nowDate.getSeconds();
      return (
        year +
        "-" +
        month +
        "-" +
        date +
        " " +
        hour +
        ":" +
        minute +
        ":" +
        seconds
      );
    },
    openGroupInfo(account) {
      if(!!account){
        ipcRenderer.getUserInfo(account, data => {
          this.$bus.emit("openPersonInfoModel", data);
        });
      }else{
        this.$message.warning(this.$t('last.searchNone'));
      }
    },
    handleDownloads(url) {
      window.open(url);
    },
    updateAbstract(){
      var data = {
        confId:this.meetingInfoModel.confId,
        historyConf:1,
        abstractData:this.abstractContent,
        abstractType:2
      }
      RL_MEET.UpdateSummary(data,
        response=>{
          console.log(response)
          let result = JSON.parse(response.result)
          console.log(result)
          if(result.statusCode=='000000'){

          }
        },
        function(err) {
          console.log(err)
          that.$message({
            message: err.statusMsg,
            type: "warning"
          });
        }
      )
    },
    downRecord(item){
      console.log(item)
      // window.open(item.mp4.confId);
      let data = {
