					color: rgba(0,0,0,0.8);
					// margin-top: Vh(2);
					line-height: Vh(22);
					text-align: center;
					cursor: pointer;
					margin-right: Vw(20);
				}
			}
		}
		::-webkit-scrollbar {
			display: none; /* Chrome Safari */
		}
  }
</style>

<style lang="scss">
  @function Vh($col) {
    @return($col/1080)*100vh;
  }
  @function Vw($col) {
    @return($col/1920)*100vw;
  }

  .CloudMeet_New{
    .el-checkbox__input{
      float: left;
      position: relative;
      width: Vh(14);
      height: Vh(14);
      margin-top: Vh(8);
    }
    .start{
      .item{
        li.password{
          .el-switch{
            font-size: Vh(15);
            line-height: Vh(40);
            float: right;
            position: absolute;
            top: 50%;
            margin-top: -10px;
            right: 0;
            .el-switch__core{
              line-height: Vh(40);
            }
          }
          .el-switch.is-checked .el-switch__core{
            border-color: #2570FF;
            background-color: #2570FF
          }
        }li.confName,li.max-member,.pwd{
          .el-input--small {
            .el-input__inner {
              width: 100%;
              height: Vh(29);
              line-height: Vh(29);
              opacity: 0.4;
              font-size: Vh(16);
              text-align: right;
              color: #000000;
              border: none;
              padding: 0;
            }
          }
        }
        li.confStartTime{
          .el-date-editor--time.el-date-editor.el-input{
            width: Vw(100);
          }
					.date_picker .el-picker__footer .el-button--text {
						display: none;
					}
          .el-date-editor.el-input{
            width: Vw(160);
            .el-input__inner{
              width: 100%;
              padding-left: Vw(30);
              padding-right: Vw(10);
              font-size: Vh(13);
              text-align: left;
              height: Vh(29);
              line-height: Vh(29);
            }
            .el-input__prefix{
              left: Vw(8);
              .el-input__icon{
                width: Vw(20);
                line-height: Vh(29);
              }
            }
          }
        }
        .pwd{
          .el-input--small{
            float: left;
            width: Vw(150);
            .el-input__inner{
              text-align: left;
              width: Vw(180);
            }
          }
        }
        li.confTime{
          overflow: hidden;
          .el-select{
            float: left;
            width: Vw(97)!important;
            margin-left: Vw(20);
            .el-input--small{
              width: Vw(97);
              .el-input__inner{
                font-size: Vh(13);
                padding: 0 0 0 Vw(10);
                text-align: left;
                height: Vh(29);
                line-height: Vh(29);
              }
              .el-input__suffix{
                right: 0;
                .el-select__caret{
                  height: Vh(29);
                  line-height: Vh(29);
                  font-size: Vh(14);
                }
              }
            }
          }
        }
      }
    }
    .btns{
      .el-button{
        width: Vw(138);
        height: Vh(54);
        border-radius: Vh(5);
        padding: 0;
        font-size: Vh(20);
      }
      .cancel{
        color: #2570FF;
      }
      .cancel:hover{
        color: #2570FF;
        background: #fff;
      }
    }
    .join{
      .el-input--small{
        .el-input__inner{
          line-height: Vh(60);
          height: Vh(60);
          font-size: Vh(18);
          padding-left: Vw(55);
          color: rgba(112,112,112,0.64);
          border-color: rgba(112,112,112,0.64);
        }
        .el-input__icon{
          font-size: Vh(24);
          width: Vw(55);
          color: rgba(33,30,30,0.57);
        }
      }
    }
		.meetingTitle{
			.el-tag--dark.el-tag--success {
				background-color: #67c23a;
				border-color: #67c23a;
				color: #fff;
			}
		}
  }
</style>




