<template>
    <div style="height:100%" class="meetinfo">
      <div class="meetingTitle">
					<div class="mainTit">
						<span class="title">{{meetingInfoModel.confName}}</span>
						<span class="meetingStatus"><el-tag v-if="meetingInfoModel.state" type="danger" effect="dark" size="mini">进行中</el-tag><el-tag v-else type="success" effect="dark" size="mini">未开始</el-tag></span>
						<span class="share" @click="shareMeeting">分享</span>		
					</div>								
			</div>
			<div class="right-main">
				<div class="con infoMeeting">
					<el-card shadow="always" class="basicInfoCard">
						<ul>
							<li style="margin-bottom:10px">
								<span>会议号：<span>{{meetingInfoModel.confId}}</span><i class="el-icon-document-copy" style="color:#4f98ff" @click="copyConfId"></i></span>
								<span style="float:right">创建者：{{meetingInfoModel.holder}}</span>
							</li>
							<li>
								<span>时间：{{meetingInfoModel.state?meetingInfoModel.startTime:meetingInfoModel.reserveStartTime}}</span>
								<span style="float:right">会议密码：{{meetingInfoModel.password?meetingInfoModel.password:'无'}}</span>
							</li>
						</ul>
					</el-card>
					<el-card shadow="always" class="topicCard" >
						<p>会议议题<span v-if="LoginServe.UserId==meetingInfoModel.holderAccount" @click="openEditModal">编辑<img src="../../assets/img/meet/start-edit.png" alt=""></span></p>
						<ul v-if="meetingTopicsList&&meetingTopicsList.length>0">
							<li v-for="(item,index) in meetingTopicsList " :key="index">
								<span>{{index+1+'.'}}</span> {{item.value}}
							</li>
						</ul>
						<ul v-else>						
							<p >{{LoginServe.UserId==meetingInfoModel.holderAccount?'您可以添加会议议题!':'暂无会议议题'}}</p>
						</ul>
					</el-card>
					<el-card shadow="always" class="memberCard">
						<div class="tit">
							<span class="flt">参会人 ({{meetingInfoModel.members?meetingInfoModel.members.length:0}})</span>
							<img src="../../assets/img/meet/start-add.png" alt="" class="flt" @click.prevent="inviteMember">
							<img v-if="LoginServe.UserId==meetingInfoModel.holderAccount" src="../../assets/img/meet/start-del.png" alt="" class="flt" @click.prevent="kickoutMember">
						</div>
						<ul>
							<li v-for="(member,index) in meetingInfoModel.members" :key="index">
								<span v-if="!member.photourl" :style="{backgroundImage:getcolor(member.account)}">{{member.userName || member.username | nameslice}}</span>
								<img v-else :src="member.photourl" alt="">
								<p>{{member.userName||member.username}}</p>
							</li>
						</ul>
					</el-card>
					<el-card shadow="always" class="videoCard" v-if="false">
						<div class="tit">
							<span class="flt">云录像</span>
						</div>
						<ul class="radioUl">
							<el-radio-group v-model="radio">
								<el-radio :label="3">主持人视角</el-radio>
								<el-radio :label="6">参会人视角</el-radio>
							</el-radio-group>
							<span style="float:right">选择录像源<i class="el-icon-video-camera-solid" style="margin-left:10px"></i></span>
						</ul>		
					</el-card>						
					<div>
						<div class="btns" v-if="meetingInfoModel.state" style="display:flex;justify-content: center;">
							<el-button  class="cancel flt" @click="cancelMeetingInfo">返回</el-button>
							<el-button v-if="LoginServe.UserId==meetingInfoModel.holderAccount" class="cancel flt" @click="handleCancelMeet">结束会议</el-button>
							<el-button type="primary" class="frt" @click="checkConnect(meetingInfoModel.confId)" >重新入会</el-button>
						</div>
						<div class="btns" v-else style="display:flex;justify-content: center;" >
							<el-button class="cancel"   @click="cancelMeetingInfo">返回</el-button>
							<el-button class="cancel" v-if="LoginServe.UserId==meetingInfoModel.holderAccount" @click="handleCancelReserve">取消预约</el-button>
							<el-button type="primary" v-if="LoginServe.UserId==meetingInfoModel.holderAccount" @click="editReserveMeeting(meetingInfoModel)" >编辑</el-button>
						</div>
					</div>
				</div>
			</div>
			<div class="shadow" v-if="isShowEditTopic">
				<div class="editTopicModal">
					<!-- <span class="frt" @click="submitForm('dynamicValidateForm',historyConf)">完成</span>		 -->
						<ul style="margin-top:50px">
							<el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
								<el-form-item
									v-for="(topic, index) in dynamicValidateForm.topics"
									:label="index+1+''"
									:key="topic.key"
									:prop="'topics.' + index + '.value'"
									:rules="{required: true, message: '会议议题不能为空', trigger: 'blur'}"
								>
									<el-input v-model="topic.value" style="width:80%" maxlength="40"></el-input> <el-button icon="el-icon-delete" circle @click.prevent="removeTopic(topic)"></el-button>
								</el-form-item>
							</el-form>
						</ul>		
					<span class="frt" @click="addTopic">添加</span>			
					<div class="topicBottom">
						<span  @click="cancelTopic">取消</span>
						<span  @click="submitForm('dynamicValidateForm',historyConf)">完成</span>
					</div>	
				</div>
			</div>
			<el-dialog title="踢出会议成员"	:visible.sync="isShowKickoutModal"	width="30%">
				<!-- <ul class="kikoutContentUl">
					<li class="kikoutContent" v-for="(item,index) in cankikoutMembersList" :key="index">
						<el-checkbox @change="checkMember($event,item)">
							<span v-if="!item.photourl" :style="{backgroundImage:getcolor(item.account)}">{{item.userName || item.username | nameslice}}</span>
							<img v-else :src="item.photourl" alt="">
							<p>{{item.userName||item.username}}</p>
						</el-checkbox>					
					</li>
				</ul> -->
				<el-checkbox-group v-model="kikoutList">
					<el-checkbox class="kikoutContent" v-for="(item,index) in cankikoutMembersList" :key="index" :label="item">
								<span v-if="!item.photourl" :style="{backgroundImage:getcolor(item.account)}">{{item.userName || item.username | nameslice}}</span>
							<img v-else :src="item.photourl" alt="">
							<p>{{item.userName||item.username}}</p>
					</el-checkbox>
				</el-checkbox-group>
				<span slot="footer" class="dialog-footer">
					<el-button @click="isShowKickoutModal = false">取 消</el-button>
					<el-button type="primary" @click="handleKikout">确 定</el-button>
				</span>
			</el-dialog>
			<meetShare :isshow="meetShareDialog" :meetInfos="shareMeetingInfo"></meetShare>
			<el-dialog
				title="提示"
				:visible.sync="isShowDelete"
				width="30%">
				<p style="text-align:center">{{dialogContent}}</p>
				<span slot="footer" class="dialog-footer">
					<el-button @click="isShowDelete = false">取 消</el-button>
					<el-button type="primary" @click="closeMeeting(meetingInfoModel.confId)">确 定</el-button>
				</span>
			</el-dialog>			
    </div>
</template>
<script>
import {mapGetters,mapState} from 'vuex';
import meetShare from "../meeting/meetShare"; //延时弹框
export default {
  props: ['confId','historyConf'],
  components: {
		meetShare
  },
  data(){
    return {
			meetShareDialog:false,
			isShowKickoutModal:false,
      meetingId:'',
      meetingTopicsList:[],
      membersList:[],
			cankikoutMembersList:[],
			kikoutList:[],
      radio:3,
      isShowEditTopic:false,
      meetingInfoModel:{},
      dynamicValidateForm: {
        topics: [{
          value: ''
        }],
      },
			shareMeetingInfo:{},
			isShowDelete:false,
			dialogContent:'',
			maxSelectMemberLength:0
    }
  },
  computed: {
    ...mapState(["LoginServe"]),
    ...mapGetters([
      'user_info',
    ]),
  },
  watch:{
    confId:function(){
      console.log(this.confId)
    }
  },
  created(){
	this.RX_Util.registerBusEvents({
		selectMemeberCallBack: this.addMemeberCallBack
	}, this);
  },
  methods:{
 		getMeetInfo(confId,historyConf){
			let that = this;
      let GetMeetInfoBuilder = new RL_MEET.GetMeetInfoBuilder();	
      GetMeetInfoBuilder.setConfId(confId);
      GetMeetInfoBuilder.setHistoryConf(historyConf);
     	RL_MEET.GetMeetInfo(GetMeetInfoBuilder,
        response => {
          console.log(response)
          if (response.code == "200") {
            let result = JSON.parse(response.result)			
            console.log(result)
            this.meetingTopicsList = []
            console.log(this.meetingTopicsList)
            result.members.forEach(m=>{					
              if(m.deviceType){
                var idx = m.memberId.indexOf('@')								
                m.memberId = idx>0? m.memberId.substring(0,idx):m.memberId
              }
              m.account =  m.memberId;
              if(m.roleId==1){
                result.holder = m.userName;
				result.holderAccount = m.account
              }
            })
            console.log(result.members)
            this.membersList = [...result.members]
            console.log(this.meetingInfoModel)
            let topics = result.confTopic?JSON.parse(result.confTopic).topics:[]
            topics.forEach(item=>{
              item.value = item.value?item.value:item.topic
            })
            result.topics = [...topics];
            this.meetingInfoModel = Object.assign({},result);
            this.meetingTopicsList = [...topics];		
						this.maxSelectMemberLength = this.meetingInfoModel.maxMember - this.membersList.length
          } else {
            this.$message({
              message:
                response.data.statusMsg + this.$t('meeting.errorCode') +
                response.data.statusCode,
              type: "error"
            });
          }
        },
        function(err) {
          if (err.statusCode == "814028") {
            // that.conidpop = err.confId;
            // that.confirmleave = true;
          } else {
            that.$message({
              message: err.statusMsg,
              type: "warning"
            });
          }
        }
      )			
    },
    copyConfId(){
      let oInput = document.createElement('input');
      oInput.value = this.meetingInfoModel.confId;
      document.body.appendChild(oInput);
      oInput.select(); // 选择对象;
      console.log(oInput.value)
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message({
        message: '已成功复制到剪切板',
        type: 'success'
      });
      oInput.remove()
    },
    openEditModal(){
      let that = this;
      this.isShowEditTopic = true;
      if(this.meetingInfoModel.topics&&this.meetingInfoModel.topics.length>0){
					that.dynamicValidateForm.topics = []
					that.meetingTopicsList.forEach((item,index)=>{
						that.dynamicValidateForm.topics.push({value:item.value,key:index})
					})
				}else{
					this.dynamicValidateForm={
						topics: [{
							value: ''
						}],
        	}
				}
    },
	  inviteMember(){
			this.maxSelectMemberLength = this.meetingInfoModel.maxMember - this.membersList.length
			this.$bus.emit('addPerson',true, this.maxSelectMemberLength, this.membersList, this.addMemeberCallBack, true)
			console.log(this.membersList)			
    },
		kickoutMember(){
			this.isShowKickoutModal = true;
			this.kikoutList = [];
			this.membersList.forEach(item=>{
				item.checked = false;
			})
			this.cankikoutMembersList = this.membersList.filter(item=>{
				return item.roleId !=1
			})
			console.log(this.membersList)
		},
    cancelMeetingInfo(){
      this.$emit('cancelMeetingInfo');
    },
    closeMeeting(confId){
			let that = this;
			console.log(confId)
			RL_MEET.DeleteMeeting(confId,
				response=>{
					console.log(response)
					let result = JSON.parse(response.result)
					if(result.statusCode=='000000'){
						that.$emit('cancelMeetingInfo',true)
						that.isShowDelete = false
					}	else {
						this.$message({
							message:
								result.statusMsg + this.$t('meeting.errorCode') +
								result.statusCode,
							type: "error"
						});
					}					
				},
				function(err) {
						that.$message({
							message: err.statusMsg,
							type: "warning"
						});
				}			
			)
		},
		checkConnect(confId){
			this.$bus.emit('leaveAllMeet',true)
			this.JoinMeet(confId)
		},
    JoinMeet(confId){
      console.log(confId)
      if (!confId) {
        this.$message({
          message: this.$t('meeting.pleaseEnter') + this.$t('meeting.meetingNumber'),
          type: "error"
        });
        return;
      }
      let self = this
      // sessionStorage.setItem("IsCheckedOpenAudio", this.IsCheckedOpenAudio);
      // sessionStorage.setItem("IsCheckedOpenVideo", this.IsCheckedOpenVideo);
      var conferenceMsgBuilder = new  RL_Media.ConferenceMsgBuilder();
      conferenceMsgBuilder.setPath("/REST/Conference/Info?source=SDK");
      conferenceMsgBuilder.setContent({
        appId: self.LoginServe.AppId,
        userId: self.LoginServe.UserId,
        confId: confId
      });
      RL_Media.ConferenceMsg(
        conferenceMsgBuilder,
        function(response) {
          var resp = JSON.parse(response.result);
					console.log('resp==================',resp)
          if (resp.statusCode == "000000") {
            if ((resp.state & 0x0200) == 512) {
              if (!resp.password) {
                self
                  .$prompt("", this.$t('meeting.pleaseEnter') + this.$t('meeting.meetingPassword'), {
                    confirmButtonText: this.$t('btn.determine'),
                    center: true,
                    showCancelButton: false,
                    inputPattern: /^\d{6}$/,
                    inputErrorMessage: this.$t('meeting.pleaseEnterPassword'),
                    inputType: "number",
                    inputValue: this.$t('meeting.pleaseEnterPassword'),
                    inputPlaceholder: this.$t('meeting.pleaseEnterPassword')
                    // maxlength:6,
                    // cancelButtonText: "取消",
                    // inputType: 'password',
                    // inputValidator: joinMeetPwd
                  })
                  .then(({ value }) => {
                    if (value) {
						self.$router.push({
							path: '/yuntongxun/meeting',
							query: {confId}
						})
                    } else {
                      self.$message({
                        message: this.$t('meeting.pleaseInputPassword'),
                        type: "error"
                      });
                    }
                  })
                  .catch(function() {});
              } else {
				self.$router.push({
					path: '/yuntongxun/meeting',
					query: {confId}
				})
              }
            } else {
				self.$router.push({
					path: '/yuntongxun/meeting',
					query: {confId}
				})
            }
          } else if (resp.statusCode == "814006") {
            self.$message.error(self.$t('meeting.wrongMeetingNumber'));//会议号错误
          } else if (resp.statusCode == "814026") {
            self.$message.error(self.$t('meeting.meetingNotStart'));//会议未开始
          } else if (resp.statusCode == "814012") {
            self.$message.error(self.$t('meeting.meetingLocked'));//会议已锁定
          } else {
            self.$message({
              message: resp.statusMsg + ";"+self.$t('meeting.errorCode')+"：" + resp.statusCode,
              type: "error"
            });
          }
        },
        function(err) {
          console.log(err);
        }
      );
    },
    submitForm(formName,historyConf){
      console.log('historyConf===========',historyConf)
      // this.isShowEditTopic = false;
      let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
						console.log(this.dynamicValidateForm)
						that.meetingTopicsList = [...that.dynamicValidateForm.topics]
						console.log('that.meetingTopicsList====================',	that.meetingTopicsList)
						console.log('this.meetingInfoModel=======================',this.meetingInfoModel)
						if(this.meetingInfoModel.confId){
							this.updateMeetingTopic(this.meetingInfoModel,historyConf)
						}
						if(that.dynamicValidateForm.topics.length==0){
							that.dynamicValidateForm.topics.push({
								value: '',
								key: Date.now()
							});
						}
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
		cancelTopic(){
			let that = this;
      this.isShowEditTopic = false;
      if(that.dynamicValidateForm.topics.length==0){
        that.dynamicValidateForm.topics.push({
          value: '',
          key: Date.now()
        });
      }			
		},
    //更新会议议题
		updateMeetingTopic(meet,historyConf){
				let that = this;
				console.log(meet)
				// let contentTopics = {
        //   topics: this.meetingTopicsList
        // }
				let topics = [];
				this.meetingTopicsList.forEach(item=>{
					topics.push({
						topic:item.value
					})
				})
				let contentTopics = {
          topics
        }
				console.log(contentTopics)
				let ConferenceUpdateBuilder = new RL_MEET.ConferenceUpdateBuilder();	
				ConferenceUpdateBuilder.setConfId(meet.confId)
				ConferenceUpdateBuilder.setConfTopic(JSON.stringify(contentTopics))
				RL_MEET.ConferenceUpdate(
					ConferenceUpdateBuilder,
					response => {
            this.isShowEditTopic = false
						console.log(response)
						if (response.statusCode == "000000") {
              
						} else {
							// that.getMeetInfo(meet.confId,historyConf)
							this.$message({
								message:
									response.data.statusMsg + this.$t('meeting.errorCode') +
									response.data.statusCode,
								type: "error"
							});
						}
					},
					function(err) {
						// that.getMeetInfo(meet.confId,historyConf)
						if (err.statusCode == "814028") {
							that.conidpop = err.confId;
							that.confirmleave = true;
						} else {
							that.$message({
								message: err.statusMsg,
								type: "warning"
							});
						}
					}
				);
		},
    removeTopic(item){
      var index = this.dynamicValidateForm.topics.indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm.topics.splice(index, 1)
      }		
    },
    addTopic(){
      if(this.dynamicValidateForm.topics.length>=3){
				this.$message({
					type:'warning',
					message:'最多编辑三条议题'
				})
			}else {
				this.dynamicValidateForm.topics.push({
					value: '',
					key: Date.now()
				});
			}
    },
		addMemeberCallBack(selectList){
			let that = this
			console.log('confid=========================',this.meetingInfoModel.confId)
			// console.log(selectList)
			if(!selectList||selectList.length==0){
				this.$message.warning('未选择成员')
				return
			}
			let members = []
			selectList.forEach(item=>{
				members.push({
					memberId: item.account,
					idType: 2,
					userName: item.username||item.account,
				})
			})
			let InviteMemberBuilder = new RL_MEET.InviteMemberBuilder();
			InviteMemberBuilder.setInviteMembers(members);
			InviteMemberBuilder.setConfId(this.meetingInfoModel.confId);
			InviteMemberBuilder.setCallImmediately(1);
			console.log('InviteMemberBuilder====================',InviteMemberBuilder)
			RL_MEET.InviteMember(InviteMemberBuilder,
				response => {
            // this.isShowEditTopic = false
						console.log(response)
						if (response.statusCode == "000000") {
              				this.$message({message:'邀请成功',type:'success'})
							this.getMeetInfo(this.meetingInfoModel.confId,0)
							that.$bus.emit('addPerson', false);
							that.membersList = [...that.membersList, ...members]
						} else {
							// that.getMeetInfo(meet.confId,historyConf)
							this.$message({
								message:
									response.data.statusMsg + this.$t('meeting.errorCode') +
									response.data.statusCode,
								type: "error"
							});
						}
					},
					function(err) {
						// that.getMeetInfo(meet.confId,historyConf)
						console.log('err============',err)
							that.$message({
								message: err.statusMsg,
								type: "warning"
							});
					}
			)
		},
		handleKikout(){
			this.isShowKickoutModal = false;
			console.log(this.kikoutList)
			let members = []
			this.kikoutList.forEach(item=>{		
				members.push({
					memberId: item.deviceType?item.account+'@'+item.deviceType:item.account,
					idType: 2,
					userName: item.username,
				})
			})
			console.log(members)
			let KickMeetMemberBuilder = new RL_MEET.KickMeetMemberBuilder();
			KickMeetMemberBuilder.setKickMembers(members);
			KickMeetMemberBuilder.setConfId(this.meetingInfoModel.confId);
			console.log('KickMeetMemberBuilder==============',KickMeetMemberBuilder)
			RL_MEET.KickMeetMember(KickMeetMemberBuilder,
				response => {
						console.log(response)
						let result = JSON.parse(response.result);
						
						if (result.statusCode == "000000") {
              this.$message({message:'踢出成功',type:'success'})
							this.getMeetInfo(this.meetingInfoModel.confId,0)
							this.kikoutList = []
						} else {
							// that.getMeetInfo(meet.confId,historyConf)
							this.$message({
								message:
									response.data.statusMsg + this.$t('meeting.errorCode') +
									response.data.statusCode,
								type: "error"
							});
						}
					},
					function(err) {
						// that.getMeetInfo(meet.confId,historyConf)
							that.$message({
								message: err.statusMsg,
								type: "warning"
							});
					}
			)
		},
		checkMember(val,item){
			console.log(val,item);
			if(val){
				this.kikoutList.push(item)
			}else{
			this.kikoutList =	this.kikoutList.filter(ele=>{
					return ele.account!=item.account
				})
			}
			console.log(this.kikoutList)
		},
		shareMeeting(){
			this.meetShareDialog = true;
			this.shareMeetingInfo = this.meetingInfoModel;
		},
		closeDialog() {
      this.meetShareDialog = false;
			this.shareMeetingInfo = {};
    },
		editReserveMeeting(data){
			this.$bus.emit("editReserveMeeting", data);
		},
		handleCancelReserve(){
			this.isShowDelete = true;
			this.dialogContent = '确定要取消预约的会议吗';
		},
		handleCancelMeet(){
			this.isShowDelete = true;
			this.dialogContent = '确定要结束当前会议吗';
		}
  },
}
</script>
<style lang="scss" scoped>
    @function Vh($col) {    
    	@return($col/1080)*100vh;
    }
	@function Vw($col) {
		@return($col/1920)*100vw;
	}
   .flt{
    float: left;
  }
